import React, { useEffect, Fragment, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Spin } from "antd";
import WithFilter from "../../screens/common/WithFilter";
import EmptyRecords from "../../components/Common/EmptyRecords";
import ProcurementTenderContent from "../../screens/Procurement/Tender/ProcurementTenderContent";
import ProcurementTenderFilter from "../../screens/Procurement/Tender/ProcurementTenderFilter";
import ProcurementTenderFooter from "../../screens/Procurement/Tender/ProcurementTenderFooter";
import ProcurementTenderHeader from "../../screens/Procurement/Tender/ProcurementTenderHeader";
import {
  getProcurementTenderListRequestReset,
  getProcurementTenderListInProgress,
} from "../../../redux/Procurement/Tender/ProcurementTenderAction";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../utilz/i18n";

function ProcurementTenderPage() {
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const {
    selectedCompany,
    user: { data: userDetails },
  } = useSelector((state) => state.auth);
  const { tenderList } = useSelector((state) => state.procurementTender);

  useEffect(() => {
    dispatch(getProcurementTenderListRequestReset());
    if (selectedCompany?.companyId) {
      const urlParams = new URLSearchParams(window.location.search);
      const tenderId = urlParams.get("tenderId");
      dispatch(
        getProcurementTenderListInProgress({
          ...tenderList.req,
          tenderID: tenderId || "",
        })
      );
      // dispatch(getProcurementTenderListInProgress());
    }
  }, [selectedCompany, userDetails, dispatch]);
  return (
    <div className="w-100 h-100 overflow-hidden">
      <WithFilter
        isPageTitle={false}
        headerContent={<ProcurementTenderHeader />}
        filterTitle={t("translation:procurement.filters.tenderSearch")}
        filterElement={<ProcurementTenderFilter />}
        contentElement={
          <Fragment>
            {tenderList.progressing === true && (
              <Spin
                className="d-flex justify-content-center align-items-center h-100"
                size={"large"}
                tip={t("translation:default.loading")}
                spinning={tenderList.progressing}
              />
            )}
            {!tenderList.progressing && !tenderList?.data?.length && (
              <EmptyRecords />
            )}
            {tenderList?.data?.length && <ProcurementTenderContent />}
          </Fragment>
        }
        footerContent={<ProcurementTenderFooter />}
      />
    </div>
  );
}

export default ProcurementTenderPage;
