import { Col, Row, Spin } from "antd";
import { useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";

import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import ProcurementTenderListFilter from "./Component/Header/ProcurementTenderListFilter";
import { ReactComponent as CopyIcon } from "../../../../assets/icons/copy.svg";
import { Link, useLocation } from "react-router-dom";
import Button from "../../../components/Buttons/Button";

import { APP_ROUTES } from "../../../../contants/RoutesEnum";
import {
  getTenderCopyInProgress,
  getTenderCopyRequestReset,
} from "../../../../redux/Procurement/Tender/ProcurementTenderAction";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const ProcurementTenderHeader = () => {
  const [color, setColor] = useState(false);
  const { activeTender, TenderCopy } = useSelector(
    (state) => state.procurementTender
  );
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const tenderCopyHandler = () => {
    dispatch(getTenderCopyRequestReset());

    dispatch(
      getTenderCopyInProgress({
        TenderID: activeTender?.data?.tenderID || "",
        action: "edit",
      })
    );
  };

  return (
    <Row justify="space-between" className={`py-15 px-14 align="middle`}>
      <Col className="flex-1">
        {color === true && (
          <Spin
            className="d-flex justify-content-center align-items-center h-100"
            size={"large"}
            tip={t("translation:default.loading")}
            spinning={true}
          />
        )}
        {color === false && (
          <Row align={"middle"} gutter={20}>
            <Col sm={12}>
              <ProcurementTenderListFilter />
            </Col>
            <Col sm={12}>
              <div className="d-flex justify-content-end gap-2 align-items-center">
                <Link to={APP_ROUTES.ADD_TENDER_PROCUREMENT}>
                  <Button>{t("translation:procurement.createTender")}</Button>
                </Link>
                <div
                  className="d-flex justify-content-end align-items-center"
                  onClick={() => tenderCopyHandler()}
                >
                  <CopyIcon />
                  <PrimaryLabel normal={true} className="font-weight-400 ml-5">
                    {t("translation:default.buttons.copy")}
                  </PrimaryLabel>
                </div>
              </div>
            </Col>
          </Row>
        )}
      </Col>
    </Row>
  );
};

export default ProcurementTenderHeader;
