import { Collapse, Form, Row, Col } from "antd";
import { Fragment, useEffect, useState, useMemo } from "react";
import { useSelector } from "react-redux";
import FormDatePicker from "../../../../../components/Inputs/FormDatePicker";
import FormSelectBox from "../../../../../components/Inputs/FormSelectBox";
import FormLabel from "../../../../../components/Labels/FormLabel";
import ProjectNameSelector from "../../../../../components/Selectors/ProjectNameSelector";
import TenderNameSelector from "../../../../../components/Selectors/TenderNameSelector";

const { Panel } = Collapse;
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import FormInput from "../../../../../components/Inputs/FormInput";

const ProcurementTenderFilterContent = ({ form }) => {
  const projectID = Form.useWatch("projectID", form);
  const tenderStartFromDate = Form.useWatch("tenderStartFromDate", form);
  const tenderStartToDate = Form.useWatch("tenderStartToDate", form);
  const tenderEndFromDate = Form.useWatch("tenderEndFromDate", form);
  const tenderEndToDate = Form.useWatch("tenderEndToDate", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const handleProjectChange = (data) => {
    form.setFieldsValue({
      tenderID: undefined,
      projectID: data?.value,
    });
  };

  return (
    <div className="px-16">
      <Row>
        <Col sm={24}>
          <ProjectNameSelector
            placeholder={t("translation:procurement.projectName.placeholder")}
            label={t("translation:procurement.projectName.label")}
            rules={[]}
            name={"projectID"}
            editMode={true}
            margin={"mb-15"}
            onChange={handleProjectChange}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <TenderNameSelector
            editMode={true}
            label={"Tender Name"}
            placeholder={"Tender Name"}
            name={"tenderID"}
            projectID={projectID}
            mode="multiple"
            rules={[]}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24} className={"mb-16"}>
          <FormLabel>
            {t("translation:procurement.fields.date.tenderStartDate")}
          </FormLabel>
          <Row className={"mt-3 align-items-center"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.from.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"tenderStartFromDate"}
                disableFuture={true}
                disabledTargetDate={tenderStartToDate}
              />
            </Col>
          </Row>
          <Row className={"mt-6 align-items-center mt-16"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.to.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                name="tenderStartToDate"
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                disableFuture={false}
                disabledTargetDate={tenderStartFromDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <FormLabel>Tender End Date</FormLabel>
          <Row className={"mt-3 align-items-center"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.from.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"tenderEndFromDate"}
                disableFuture={true}
                disabledTargetDate={tenderEndToDate}
              />
            </Col>
          </Row>
          <Row className={"mt-6 align-items-center mt-16"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.to.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                name="tenderEndToDate"
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                disableFuture={false}
                disabledTargetDate={tenderEndFromDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default ProcurementTenderFilterContent;
