import React, { useEffect, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  MODULES_ID,
  NOTIFICATION_TYPES,
} from "../../../../../../../contants/common";
import useDocsExtentions from "../../../../../../../redux/hooks/useDocsExtentions";
import { Spin } from "antd";
import _, { remove } from "lodash";
import {
  getTenderDocumentListEffect,
  getTenderListEffect,
  TenderFileDeleteEffect,
  TenderFileUpdateEffect,
  TenderFileUploadEffect,
} from "../../../../../../../redux/Procurement/Tender/ProcurementTenderEffects";
import { addNewFileBtn } from "../../../../../../../redux/visibility/VisibilityActions";
import { triggerNotification } from "../../../../../../../utilz/utilz";
import { setAppLoader } from "../../../../../../../redux/common/CommonActions";
import ProcurementFileDetailsCard from "../../../../Project/Component/DetailsCard/Tabs/ProcurementFileDetailsCard";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { getProjectListEffect, procurementFileDeleteEffect, procurementFileUploadEffect, procurementFileUploadUpdateEffect } from "../../../../../../../redux/Procurement/Project/ProcurementProjectEffects";

export default function TenderDocumentationTab(props) {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const { documentDetails } = useSelector((state) => state.shipment);
  const allowedExtentions = useDocsExtentions(MODULES_ID.SHIPMENT);
  const [documentsList, setDocumentsList] = useState([]);
  const { addnewfileactions } = useSelector((state) => state.visibility);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const { activeTender } = useSelector((state) => state.procurementTender);
  const [isLoader, setIsLoader] = useState(false);

  useEffect(() => {
    loadList();
  }, [activeTender?.data?.projectID, activeTender?.data?.tenderID]);

  const loadList = async () => {
    dispatch(setAppLoader(true));
    setIsLoader(true);
    let { data } = await getTenderListEffect({
      projectId: activeTender?.data?.projectID,
      tenderID: activeTender?.data?.tenderID,
      isApproved: true,
    });
    dispatch(setAppLoader(false));
    let cloneList = _.cloneDeep([]);
    data.tenderViewModel?.[0].tenderDocument.forEach((item) => {
      cloneList.push({
        renderKey: _.uniqueId(),
        isNew: false,
        isUpdate: false,
        ...item,
        file: item.documentName,
        fileGuid: item?.documentPath?.split(".")[0],
        comments:item.fileComment,
        projectID: activeTender?.data?.projectID,
        tenderID: activeTender?.data?.tenderID,
      });
    });
    setDocumentsList(cloneList);
    setIsLoader(false);
  };

  useEffect(() => {
    if (props.btnClikedStatus) {
      handleNewFile();
    }
  }, [props.btnClikedStatus]);

  const handleNewFile = () => {
    let data = _.cloneDeep(documentsList);
    data = [
      {
        projectID: activeTender?.data?.projectID,
        tenderID: activeTender?.data?.tenderID,
        renderKey: _.uniqueId(),
        isNew: true,
        isUpdate: true,
      },
      ...data,
    ];
    setDocumentsList(data);
  };

  const getDynamicDocumentList = async (notificationTitle, isNew, index) => {
    try {
      setIsLoader(true);
      let { data } = await getTenderListEffect({
        projectId: activeTender?.data?.projectID,
        tenderID: activeTender?.data?.tenderID,
        isApproved: true,
      });
      let teenderData = data.tenderViewModel?.[0].tenderDocument || [];
      if (isNew && teenderData?.length) {
        let cloneList = _.cloneDeep(documentsList);
        cloneList = cloneList.filter((item) => item.isNew == false);
        let obj = teenderData[teenderData?.length - 1];
        obj = {
          ...obj,
          isNew: false,
          isUpdate: false,
          file: obj.documentName,
          fileGuid: obj?.documentPath?.split(".")[0],
          comments:obj.fileComment,
          projectID: activeTender?.data?.projectID,
          tenderID: activeTender?.data?.tenderID,
        };
        cloneList.push(obj);
        setDocumentsList([...cloneList]);
        props.addNewFileBtnEnable(false);
        setIsLoader(false);
      } else {
        let cloneList = _.cloneDeep(documentsList);
        let findItem = cloneList[index];
        let findObj = teenderData?.filter(
          (item) => item.documentID == findItem.documentID
        );
        findObj = {
          ...findObj[0],
          renderKey: findItem.renderKey,
          isNew: false,
          isUpdate: false,
          file: findObj[0].documentName,
          fileGuid: findObj[0]?.documentPath?.split(".")[0],
          comments:findObj[0]?.fileComment,
          projectID: activeTender?.data?.projectID,
          tenderID: activeTender?.data?.tenderID,
        };
        cloneList[index] = findObj;
        setDocumentsList(cloneList);
        setIsLoader(false);
      }
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.SUCCESS,
        title: notificationTitle,
      });
    } catch (error) {
      dispatch(setAppLoader(false));
      console.log(error);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t(
          "translation:default.documentManager.notificationUploadingError"
        ),
      });
    }
  };

  const handleSave = async (formData, isNew, index) => {
    dispatch(setAppLoader(true));

    try {
      if (isNew) {
        await procurementFileUploadEffect(formData);
        getDynamicDocumentList(
          t("translation:default.documentManager.notificationUploadingSuccess"),
          isNew,
          index
        );
      } else {
        await procurementFileUploadUpdateEffect(formData);
        getDynamicDocumentList(
          t("translation:default.documentManager.notificationUploadingSuccess"),
          isNew,
          index
        );
      }
      dispatch(setAppLoader(false));
    } catch (error) {
      console.log(error);
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t(
          "translation:default.documentManager.notificationUploadingFailed"
        ),
      });
    }
  };

  const editRecord = (index) => {
    let array = [...documentsList];
    array[index]["isUpdate"] = true;
    setDocumentsList(array);
  };

  const handleDelete = async (item, index) => {
    try {
      let data = _.cloneDeep(documentsList);
      if (!item.isNew) {
        dispatch(setAppLoader(true));
        let payload = {
          fileGuid: item.fileGuid,
          documentId: item.documentID,
        };

        await procurementFileDeleteEffect(payload);
        dispatch(setAppLoader(false));
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: t(
            "translation:default.documentManager.notificationDeleteSuccess"
          ),
        });
      }
      data.splice(index, 1);
      setDocumentsList(data);
    } catch (error) {
      dispatch(setAppLoader(false));
      console.log(error);
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: t("translation:default.documentManager.notificationDeleteError"),
      });
    }
  };

  const handleReset = (index, isNew) => {
    let data = _.cloneDeep(documentsList);
    if (isNew) {
      props.addNewFileBtnEnable(false);
      let findObj = data.filter((item) => item.isNew != true);
      setDocumentsList(findObj);
    } else {
      let findObj = data[index];
      findObj.isUpdate = false;
      data[index] = findObj;
      setDocumentsList(data);
    }
  };
  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
        {isLoader == true && (
          <div className="d-flex justify-content-center align-items-center h-100">
            <Spin
              spinning={isLoader}
              tip={t("translation:default.loading")}
            ></Spin>
          </div>
        )}
        {isLoader === false &&
          documentsList.map((row, index) => (
            <ProcurementFileDetailsCard
              key={row.renderKey}
              fileDetails={row}
              handleDelete={handleDelete}
              handleSave={handleSave}
              isNew={row.isNew}
              index={index}
              allowedExtentions={allowedExtentions}
              editRecord={editRecord}
              handleResetValueAction={(index, isNew) =>
                handleReset(index, isNew)
              }
            />
          ))}
        {isLoader == false && documentsList.length == 0 && (
          <div className="d-flex justify-content-center align-items-center h-100">
            {t("translation:default.notification.noRecondfound")}
          </div>
        )}
      </div>
    </div>
  );
}
