import React, { useState, useMemo, useEffect, Fragment } from "react";
import { Card, Col, Form, Row } from "antd";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { useDispatch, useSelector } from "react-redux";
import {
  getTenderCarrierListInProgress,
  getTenderCarrierListRequestReset,
} from "../../../../../../../redux/Procurement/Tender/ProcurementTenderAction";

import Button from "../../../../../../components/Buttons/Button";
import CommonModal from "../../../../../../components/Modals/CommonModal";
import TransportationProviderCard from "./TransportationProviderCard";
import EmptyRecords from "../../../../../../components/Common/EmptyRecords";
import ContentSpinner from "../../../../../../components/Spinners/ContentSpinner";
import AddTransportationProvider from "../../../../TransportationProvider/Component/AddTransportationProvider/AddTransportationProvider";

export default function TransportationProviderTab({ tenderId }) {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const dispatch = useDispatch();
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [transProvider, setTransProvider] = useState([]);

  useEffect(() => {
    dispatch(getTenderCarrierListRequestReset());
    dispatch(getTenderCarrierListInProgress({ tenderId, action: "edit" }));
  }, [dispatch, tenderId]);

  const { transportProviderList } = useSelector(
    (state) => state.procurementTender
  );

  return (
    <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
      <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
        <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
          <div className="d-flex justify-content-end mb-15">
            {isOpenModal && (
              <CommonModal
                className="w-65 h-65"
                open={isOpenModal}
                title={`${t("translation:default.buttons.search")}/${t(
                  "translation:default.buttons.add"
                )} ${t(
                  "translation:default.fields.transportationProvider.label"
                )}`}
                onClose={() => setIsOpenModal(false)}
                customFooter={<></>}
              >
                <AddTransportationProvider
                  selectedTransProvider={(data) => {
                    setTransProvider([...data]);
                    setIsOpenModal(false);
                  }}
                />
              </CommonModal>
            )}
            <Button onClick={() => setIsOpenModal(true)}>
              {`${t("translation:default.buttons.search")}/${t(
                "translation:default.buttons.add"
              )} ${t(
                "translation:default.fields.transportationProvider.label"
              )}`}
            </Button>
          </div>
          {transProvider.map((row, index) => (
            <TransportationProviderCard
              TransportationProvider={{
                ...row,
                name: row.carrierName || "",
                scac: row.carrierScacCode || "",
                isNewRecord: true,
              }}
              key={index}
              tenderId={tenderId}
              removeHandler={(data) => {
                let remainingItems = transProvider.filter(
                  (item) => item.carrierKey != data.carrierKey
                );
                setTransProvider([...remainingItems]);
              }}
            />
          ))}

          {transportProviderList.progressing ? (
            <ContentSpinner size="large" />
          ) : (
            <Fragment>
              {transportProviderList?.data?.length > 0 ? (
                <Fragment>
                  {transportProviderList?.data.map((row, index) => (
                    <TransportationProviderCard
                      TransportationProvider={row}
                      key={index}
                      tenderId={tenderId}
                    />
                  ))}
                </Fragment>
              ) : (
                <Fragment>
                  <EmptyRecords
                    title="There is no Bit records found!"
                    descriptionOne=""
                    descriptionTwo=""
                  />
                </Fragment>
              )}
            </Fragment>
          )}
        </div>
      </div>
    </div>
  );
}
