import { Col, Row } from "antd";
import React, { useMemo } from "react";
import FiledCard from "../../../../../components/Cards/FiledCard";
import BatchLabel from "../../../../../components/Labels/BatchLabel";
import PrimaryLabel from "../../../../../components/Labels/PrimaryLabel";
import Paper from "../../../../../components/Paper/Paper";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../utilz/i18n';

export default function ProcurementProjectMinCard(props) {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Paper
      className={`shipment-card border-muted ${
        props?.data?.projectID === props?.item?.projectID ? "visibility-card-active" : ""
      }`}
    >
      <Row
        className="px-15 py-14"
        onClick={() => props.handlerSelectedProject(props.item)}
        gutter={15}
      >
        <Col sm={24}>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t('translation:default.fields.projectName.label')}>
                <PrimaryLabel className="text-highlight font-weight-600">
                  {props?.item?.projectName}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={12}>
              <FiledCard title={t('translation:procurement.fields.projectStatus.label')}>
                <BatchLabel className={"mt-4 bg-success"}>
                  {props?.item.isActive === true ? t('translation:procurement.fields.projectStatus.true') : t('translation:procurement.fields.projectStatus.false')}
                </BatchLabel>
              </FiledCard>
            </Col>
          </Row>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t('translation:procurement.fields.date.projectStartDate')}>
                <PrimaryLabel normal={true} className="font-weight-400">
                  {props?.item?.projectStartDate}
                </PrimaryLabel>
              </FiledCard>
            </Col>
            <Col sm={12}>
              <FiledCard title={t('translation:procurement.projectCard.projectDescription')}>
                <PrimaryLabel normal={true} className="font-weight-400">
                  {" "}
                  {props?.item?.description}
                </PrimaryLabel>
              </FiledCard>
            </Col>
          </Row>
          <Row className="mb-15">
            <Col sm={12}>
              <FiledCard title={t('translation:procurement.fields.date.projectEndDate')}>
                <PrimaryLabel normal={true} className="font-weight-400">
                  {props?.item?.projectEndDate}
                </PrimaryLabel>
              </FiledCard>
            </Col>
          </Row>
        </Col>
      </Row>
    </Paper>
  );
}
