import { Button, Col, Form, Row } from "antd";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getProcurementHistoricalBidListInProgress,
  getProcurementHistoricalBidListRequestReset,
} from "../../../../../../../redux/Procurement/Tender/ProcurementTenderAction";
import PrimaryLabel from "../../../../../../components/Labels/PrimaryLabel";
import SecondaryLabel from "../../../../../../components/Labels/SecondaryLabel";
import HorizontalBar from "../../../../../../components/Bars/HorizontalBar";
import TransportationProviderSelector from "../../../../../../components/Selectors/AsynchSearch/TransportationProviderSelector";
import CustomerNameSelector from "../../../../../../components/Selectors/CustomerNameSelector";
import FormSelectBox from "../../../../../../components/Inputs/FormSelectBox";
import { SORTING_FILEDS_VISIBILITY } from "../../../../../../../contants/VisibilityEnums";
import { t } from "i18next";
import EmptyRecords from "../../../../../../components/Common/EmptyRecords";
import ContentSpinner from "../../../../../../components/Spinners/ContentSpinner";
import LaneInformationCard from "./LaneInformationCard";
import HistoricalBidCard from "./HistoricalBidCard";

export default function historicalBidTab({ tenderId }) {
  const [form] = Form.useForm();
  const customerKey = Form.useWatch("customerKey", form);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getProcurementHistoricalBidListRequestReset());

    dispatch(getProcurementHistoricalBidListInProgress({ tenderId: tenderId }));
  }, [tenderId]);

  const { historicalBidList } = useSelector(
    (state) => state?.procurementTender
  );
  const { data } = useSelector(
    (state) => state?.procurementTender?.transportProviderList
  );

  return (
    <Row className="h-100">
      <Col
        sm={24}
        className={"h-100 overflow-y-inherit d-flex flex-direction-column"}
      >
        <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
          <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
            <div className="custom-scroll-bar overflow-x-hidden flex-1 pl-15 pr-15">
              <Form form={form}>
                <Row gutter={15} className="align-items-end">
                  <Col className="">
                    <SecondaryLabel>Historical Bid Search</SecondaryLabel>
                  </Col>
                  <Col
                    className=" shipment-card-distance-container mr-15"
                    sm={10}
                  >
                    <hr />
                  </Col>
                </Row>
                <Row gutter={15}>
                  <Col sm={6}>
                    <CustomerNameSelector name={"customerKey"} />
                  </Col>
                  <Col>
                    <FormSelectBox
                      margin="m-0"
                      label={"Origin"}
                      placeholder={"select the origin name"}
                      name={"originName"}
                      // options={""}
                    />
                  </Col>
                  <Col sm={6}>
                    <FormSelectBox
                      margin="m-0"
                      label={"destination"}
                      placeholder={"select the destination name "}
                      name={"destinationName"}
                      // options={""}
                    />
                  </Col>
                  <Col sm={5} className="align-items-end ">
                    <Row gutter={15}>
                      <Col sm={12}>
                        <Button
                          fullWidth={true}
                          type={"default"}
                          onClick={() => {
                            form.resetFields;
                          }}
                          buttonType="button"
                        >
                          {t("translation:default.buttons.clear")}
                        </Button>
                      </Col>

                      <Col sm={12}>
                        <Button
                          fullWidth={true}
                          type={"primary"}
                          onClick={() => form.submit()}
                          buttonType="submit"
                        >
                          Search
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row>
                  <Col sm={24}>
                    {historicalBidList.progressing ? (
                      <ContentSpinner size="large" />
                    ) : (
                      <>
                        {historicalBidList?.data?.lanes ? (
                          <>
                            {historicalBidList?.data?.lanes.map(
                              (row, index) => (
                                <HistoricalBidCard
                                  LaneInformation={row}
                                  key={index}
                                />
                              )
                            )}
                          </>
                        ) : (
                          <>
                            <EmptyRecords
                              title="There is no Bit records found!"
                              descriptionOne=""
                              descriptionTwo=""
                            />
                          </>
                        )}
                      </>
                    )}
                  </Col>
                  <Col sm={12}></Col>
                </Row>
                {/* historicalBidList?.success &&
              {historicalBidList?.data?.bidParameters.map((row, index) => (
                <div className="d-flex flex-nowrap gap-1">

                </div>
              ))} */}
              </Form>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  );
}
