import React, { useEffect, useState, useMemo } from "react";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import i18n from "i18next";

const ProjectApprovedBySelector = ({
  placeholder = i18n.t("translation:default.buttons.select"),
  label = i18n.t("translation:procurement.fields.projectApproved.label"),
  rules = [],
  name,
  editMode,
  margin,
  id = "",
  tooltip = false,
  mode = "",
  loading = false,
  onChange = () => {}
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  return (
    <FormSelectBox
      loading={loading}
      options={[
        {
          label: t("translation:default.options.yes"),
          value: true,
        },
        {
          label: t("translation:default.options.no"),
          value: false,
        },
      ]}
      placeholder={placeholder}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      id={id}
      tooltip={tooltip}
      mode={mode}
      onChange={onChange}
    />
  );
};

export default ProjectApprovedBySelector;
