import { Spin } from "antd";
import React, { useEffect, Fragment, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import {
  getProcurementProjectListInProgress,
  getProcurementProjectListRequestReset,
  getUserProcurementInProgress,
  getUserProcurementRequestReset,
} from "../../../redux/Procurement/Project/ProcurementProjectActions";
import EmptyRecords from "../../components/Common/EmptyRecords";
import WithFilter from "../../screens/common/WithFilter";
import ProcurementProjectContent from "../../screens/Procurement/Project/ProcurementProjectContent";
import ProcurementProjectFilter from "../../screens/Procurement/Project/ProcurementProjectFilter";
import ProcurementProjectFooter from "../../screens/Procurement/Project/ProcurementProjectFooter";
import ProcurementProjectHeader from "../../screens/Procurement/Project/ProcurementProjectHeader";
import { routeFileSelector } from "../../../utilz/i18n";

function ProcurementProjectPage() {
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const {
    selectedCompany,
    user: { data: userDetails },
  } = useSelector((state) => state.auth);

  const { projectList } = useSelector((state) => state.procurementProject);

  useEffect(() => {
    dispatch(getProcurementProjectListRequestReset());
    dispatch(getUserProcurementRequestReset());
    if (selectedCompany?.companyId) {
      dispatch(getProcurementProjectListInProgress());
      dispatch(
        getUserProcurementInProgress({ userType: userDetails?.userType })
      );
    }
  }, [selectedCompany, userDetails, dispatch]);

  return (
    <div className="w-100 h-100 overflow-hidden">
      <WithFilter
        isPageTitle={false}
        headerContent={<ProcurementProjectHeader />}
        filterTitle={t("translation:procurement.filters.projectSearch")}
        filterElement={<ProcurementProjectFilter />}
        contentElement={
          <Fragment>
            {projectList.progressing === true &&
              projectList?.data?.length === 0 && (
                <Spin
                  className="d-flex justify-content-center align-items-center h-100"
                  size={"large"}
                  tip={t("translation:default.loading")}
                  spinning={projectList.progressing}
                />
              )}
            {projectList.progressing === false &&
              projectList?.data?.length === 0 && <EmptyRecords />}
            <ProcurementProjectContent />
          </Fragment>
        }
        footerContent={<ProcurementProjectFooter />}
      />
    </div>
  );
}
export default ProcurementProjectPage;
