import { useMemo } from "react";
import { Col, Form, Row } from "antd";
import Button from "../../../components/Buttons/Button";
import { useDispatch } from "react-redux";
import {
  getProcurementTenderListRequestReset,
  getProcurementTenderListInProgress,
} from "../../../../redux/Procurement/Tender/ProcurementTenderAction";
import { DEFAULT_DATE_FORMAT } from "../../../../contants/common";

import { PROCUREMENT_TENDER_DATE_FIELDS } from "../../../../contants/Fields";

import ProcurementTenderFilterContent from "./Component/Search/ProcurementTenderFilterContent";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";

const ProcurementTenderFilter = (props) => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const getTenderList = (data) => {
    dispatch(getProcurementTenderListRequestReset());
    for (const key in data) {
      if (PROCUREMENT_TENDER_DATE_FIELDS.includes(key) && data[key]) {
        data[key] = data[key] = data[key].format(DEFAULT_DATE_FORMAT);
      }
    }
    data.tenderID = data?.tenderID?.join(",");
    dispatch(
      getProcurementTenderListInProgress({
        ...data,
        pageSize: 10,
        pageNo: 1,
      })
    );
  };

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form onFinish={getTenderList} form={form} initialValues={{}}>
          <ProcurementTenderFilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={() => {
              form.resetFields();
            }}
            buttonType="button"
          >
            {t("translation:default.buttons.clear")}
          </Button>
        </Col>

        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            Search
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default ProcurementTenderFilter;
