import React, { useState, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { Col, Row, Collapse } from "antd";
import FormSelectBox from "../../../../../../../components/Inputs/FormSelectBox";
import { routeFileSelector } from "../../../../../../../../utilz/i18n";

const { Panel } = Collapse;

export default function SummarizeCritria() {
  const [summarizeOptions, setSummarizeOptions] = useState([]);
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  return (
    <Row>
      <Col sm={24}>
        <FormSelectBox
          loading={false}
          options={summarizeOptions}
          placeholder={t('translation:default.buttons.select')}
          label={t('translation:procurement.fields.summarizeCritria.label')}
          rules={[]}
          name={"summarizeSelectedOption"}
          editMode={true}
          margin={"mb-15"}
        />
      </Col>
      <Col sm={24}></Col>
    </Row>
  );
}
