import React, { useMemo, useEffect } from "react";
import { Col, Form, Row } from "antd";
import { SORTING_ORDERS } from "../../../../../../contants/common";
import IconButton from "../../../../../components/Buttons/IconButton";
import SvgConverter from "../../../../../components/Common/SvgConverter";
import FormSelectBox from "../../../../../components/Inputs/FormSelectBox";
import { ReactComponent as FilterAscIcon } from "../../../../../../assets/icons/filter-asc.svg";
import { ReactComponent as FilterDescIcon } from "../../../../../../assets/icons/filter-desc.svg";
import { SORTING_FILEDS_PROJECT_PROCUREMENT } from "../../../../../../contants/ProcurementEnums";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { routeFileSelector } from "../../../../../../utilz/i18n";
import HiddenFormInput from "../../../../../components/Inputs/HiddenFormInput";
import {
  getProcurementProjectListInProgress,
  getProcurementProjectListRequestReset,
  updateProcurementProjectInfoRequest,
} from "../../../../../../redux/Procurement/Project/ProcurementProjectActions";

const ProcurementProjectListFilter = () => {
  const [form] = Form.useForm();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const sortASC = Form.useWatch("sortASC", form);
  const orderBy = Form.useWatch("orderBy", form);
  const dispatch = useDispatch();
  const { projectList } = useSelector((state) => state.procurementProject);

  const handleSortingToggle = (oldType) => {
    if (oldType === SORTING_ORDERS.ASC) {
      return SORTING_ORDERS.DESC;
    } else if (oldType === SORTING_ORDERS.DESC) {
      return SORTING_ORDERS.ASC;
    }
  };

  const fetchProjectProcurement = (sortASC, orderBy) => {
    dispatch(getProcurementProjectListRequestReset());
    dispatch(
      updateProcurementProjectInfoRequest({
        ...projectList.req,
        orderBy: orderBy,
        sortASC: sortASC === "ASC" ? true : false,
        pageNo: 1,
      })
    );
    dispatch(
      getProcurementProjectListInProgress({
        ...projectList.req,
        pageNo: 1,
        orderBy: orderBy,
        sortASC: sortASC === "ASC" ? true : false,
      })
    );
  };

  useEffect(() => {
    if (sortASC && orderBy) {
      fetchProjectProcurement(sortASC, orderBy);
    }
  }, [sortASC, orderBy]);

  return (
    <Form
      className="w-100"
      form={form}
      initialValues={{ sortASC: SORTING_ORDERS.ASC }}
    >
      <Row align="middle" gutter={15}>
        <Col sm={16} lg={16}>
          <FormSelectBox
            margin="m-0"
            placeholder={`${t("translation:default.fields.sortBy.label")}...`}
            name={"orderBy"}
            options={SORTING_FILEDS_PROJECT_PROCUREMENT}
          />
        </Col>
        <Col>
          {sortASC && (
            <IconButton
              onClick={() =>
                form.setFieldValue(
                  "sortASC",
                  handleSortingToggle(sortASC)
                )
              }
            >
              <SvgConverter
                className="mr-5"
                Icon={
                  sortASC === SORTING_ORDERS.ASC
                    ? FilterAscIcon
                    : FilterDescIcon
                }
                height={18}
                width={18}
              />
            </IconButton>
          )}
        </Col>
      </Row>
      <HiddenFormInput name="sortASC" />
    </Form>
  );
};

export default ProcurementProjectListFilter;
