import { Col, Form, Row } from "antd";
import { useEffect, useState, useMemo } from "react";
import { useSelector } from "react-redux";
import FormDatePicker from "../../../../../components/Inputs/FormDatePicker";
import FormSelectBox from "../../../../../components/Inputs/FormSelectBox";
import FormLabel from "../../../../../components/Labels/FormLabel";
import ProjectNameSelector from "../../../../../components/Selectors/ProjectNameSelector";
import ProjectApprovedBySelector from "../../../Component/ProjectApprovedBySelector";
import ProjectStatusSelector from "../../../Component/ProjectStatusSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";

const ProcurementProjectFilterContent = ({ form }) => {
  const [projectApprovedByOption, setProjectApprovedByOption] = useState([]);
  const fromStartDate = Form.useWatch("fromStartDate", form);
  const endStartDate = Form.useWatch("endStartDate", form);
  const fromEndDate = Form.useWatch("fromEndDate", form);
  const endEndDate = Form.useWatch("endEndDate", form);
  const isActiveVal = Form.useWatch("isActive", form);
  const isApprovedVal = Form.useWatch("isApproved", form);
  const { userProcurement } = useSelector((state) => state.procurementProject);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    const approvedByOptionsList = userProcurement?.data?.["21"]?.map(
      ({ userName, email, userId }) => ({
        label: `${userName} (${email})`,
        value: userId,
      })
    );
    setProjectApprovedByOption(approvedByOptionsList);
  }, [userProcurement?.success]);

  return (
    <div className="px-16">
      <Row>
        <Col sm={24}>
          <ProjectStatusSelector
            loading={false}
            options={[
              {
                label: t("translation:procurement.fields.projectStatus.true"),
                value: "true",
              },
              {
                label: t("translation:procurement.fields.projectStatus.false"),
                value: "false",
              },
            ]}
            placeholder={t("translation:default.buttons.select")}
            label={t("translation:procurement.fields.projectStatus.label")}
            rules={[]}
            name={"isActive"}
            editMode={true}
            margin={"mb-15"}
            onChange= {()=>{
              form.setFieldValue('projectId',[]);
            }}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <ProjectNameSelector
            loading={false}
            placeholder={t("translation:default.buttons.select")}
            label={t("translation:default.fields.projectName.label")}
            rules={[]}
            name={"projectId"}
            editMode={true}
            mode="multiple"
            margin={"mb-15"}
            isActiveVal = {isActiveVal}
            isApprovedVal = {isApprovedVal}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <ProjectApprovedBySelector
            loading={false}
            placeholder={t("translation:default.buttons.select")}
            label={"Project Approved Status"}
            rules={[]}
            name={"isApproved"}
            editMode={true}
            margin={"mb-15"}
            onChange= {()=>{
              form.setFieldValue('projectId',[]);
            }}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <FormSelectBox
            loading={false}
            options={projectApprovedByOption}
            placeholder={t("translation:default.buttons.select")}
            label={t("translation:procurement.fields.projectApprovedBy.label")}
            rules={[]}
            name={"approvedBy"}
            editMode={true}
            mode="multiple"
            margin={"mb-15"}
          />
        </Col>
      </Row>
      <Row>
        <Col sm={24} className={"mb-16"}>
          <FormLabel>
            {t("translation:procurement.fields.date.projectStartDate")}
          </FormLabel>
          <Row className={"mt-3 align-items-center"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.from.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"fromStartDate"}
                disableFuture={true}
                disabledTargetDate={endStartDate}
              />
            </Col>
          </Row>
          <Row className={"mt-6 align-items-center mt-16"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.to.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                name="endStartDate"
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                disableFuture={false}
                disabledTargetDate={fromStartDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
      <Row>
        <Col sm={24}>
          <FormLabel>Project End Date</FormLabel>
          <Row className={"mt-3 align-items-center"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.from.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                name={"fromEndDate"}
                disableFuture={true}
                disabledTargetDate={endEndDate}
              />
            </Col>
          </Row>
          <Row className={"mt-6 align-items-center mt-16"} gutter={5}>
            <Col sm={6}>
              <FormLabel>{`${t(
                "translation:default.fields.to.label"
              )} :`}</FormLabel>
            </Col>
            <Col sm={18}>
              <FormDatePicker
                name="endEndDate"
                placeholder={t(
                  "translation:procurement.fields.date.placeholder"
                )}
                margin={"mb-0"}
                disableFuture={false}
                disabledTargetDate={fromEndDate}
              />
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
  );
};

export default ProcurementProjectFilterContent;
