import { Button, Card, Col, Popconfirm, Row } from "antd";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../../../components/Labels/PrimaryLabel";
import FlagLabel from "../../../../../../components/Labels/FlagLabel";
import { Fragment, useMemo } from "react";
import SvgConverter from "../../../../../../components/Common/SvgConverter";
import IconButton from "../../../../../../components/Buttons/IconButton";
import i18n from "i18next";
import BatchLabel from "../../../../../../components/Labels/BatchLabel";
import { ReactComponent as CloseIcon } from "../../../../../../../assets/icons/close-white.svg";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { useLocation } from "react-router-dom";
import { getLabelFromDateTime } from "../../../../../../../utilz/dates";
import { postActionToTenderCarriersEffects } from "../../../../../../../redux/Procurement/Tender/ProcurementTenderEffects";
import { useDispatch } from "react-redux";
import { setAppLoader } from "../../../../../../../redux/common/CommonActions";
import { triggerNotification } from "../../../../../../../utilz/utilz";
import { NOTIFICATION_TYPES } from "../../../../../../../contants/common";
import { useSelector } from "react-redux";

const TransportationProviderCard = ({
  TransportationProvider,
  tenderId,
  removeHandler = () => {},
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const dispatch = useDispatch();
  const notificationAPI = useSelector((state) => state.common.notificationAPI);

  const sendRFIRFQHandler = async (actionType) => {
    let msg =
      actionType == "Both"
        ? "Send RFI & RFQ"
        : actionType === "delete"
        ? "Delete"
        : `Send ${actionType}`;

    try {
      let request = {
        requestAction: actionType,
        tenderID: tenderId,
      };
      dispatch(setAppLoader(true));
      let response = await postActionToTenderCarriersEffects(request);
      if (response?.data?.success) {
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: `${msg} Successfully.`,
        });
      } else {
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.ERROR,
          title: `${msg} Error.`,
        });
      }
      dispatch(setAppLoader(false));
    } catch (error) {
      dispatch(setAppLoader(false));
      triggerNotification(notificationAPI, {
        type: NOTIFICATION_TYPES.ERROR,
        title: `${msg} Error.`,
      });
    }
  };

  const handleDeleteConfirm = (data) => {
    data?.isNewRecord === true
      ? removeHandler(TransportationProvider)
      : sendRFIRFQHandler("delete");
  };

  return (
    <Row>
      <Col
        sm={24}
        className="h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
      >
        <Card
          title={
            <Row>
              <Col sm={12}>
                <FiledCard
                  title={`${t(
                    "translation:default.fields.companyName.label"
                  )}:`}
                  textClass="d-flex align-items-center gap-half"
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {TransportationProvider?.name}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={12}>
                <FiledCard
                  title={t("translation:procurement.fields.tenderStatus.label")}
                  textClass="d-flex align-items-center gap-half"
                >
                  <BatchLabel className={"mt-4 bg-success"}>
                    {TransportationProvider?.status > 0
                      ? t("translation:procurement.fields.projectStatus.true")
                      : t("translation:procurement.fields.projectStatus.false")}
                  </BatchLabel>
                </FiledCard>
              </Col>
            </Row>
          }
          extra={
            <Popconfirm
              placement="topRight"
              title={"Are you sure to delete?"}
              onConfirm={() => handleDeleteConfirm(TransportationProvider)}
              okText="Yes"
              cancelText="Cancel"
              overlayClassName=""
            >
              <IconButton disabled={false}>
                <SvgConverter
                  disabled={false}
                  Icon={CloseIcon}
                  color={"header"}
                />
              </IconButton>
            </Popconfirm>
          }
        >
          <Fragment>
            <Row gutter={15}>
              <Col sm={6}>
                <FiledCard title={t("translation:default.fields.scac.label")}>
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {TransportationProvider?.scac}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rflStatus")}
                >
                  <FlagLabel
                    varient={
                      TransportationProvider?.rfiStatus === true
                        ? "success"
                        : "danger"
                    }
                    normal={true}
                    className="font-weight-400"
                  >
                    {TransportationProvider?.rfiStatus === true
                      ? "Acceped"
                      : t("translation:default.fields.pending")}
                  </FlagLabel>
                </FiledCard>
              </Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rfqStatus")}
                >
                  <FlagLabel
                    varient={
                      TransportationProvider?.rfpStatus === true
                        ? "success"
                        : "danger"
                    }
                    normal={true}
                    className="font-weight-400"
                  >
                    {TransportationProvider?.rfpStatus === true
                      ? "Acceped"
                      : t("translation:default.fields.pending")}
                  </FlagLabel>
                </FiledCard>
              </Col>
              <Col sm={6} className="d-flex justify-content-end">
                <Button onClick={() => sendRFIRFQHandler("RFI")}>
                  {t("translation:default.buttons.sendRFI")}
                </Button>
              </Col>
            </Row>
            <Row gutter={15} className="mt-15">
              <Col sm={6}>
                <FiledCard
                  title={i18n.t("translation:default.fields.emailAddress")}
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {TransportationProvider?.email}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rfiRequestDate")}
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {getLabelFromDateTime(
                      TransportationProvider?.rfiRequestDate
                    )}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rfqRequestDate")}
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {getLabelFromDateTime(
                      TransportationProvider?.rfpRequestDate
                    )}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6} className="d-flex justify-content-end">
                <Button onClick={() => sendRFIRFQHandler("RFQ")}>
                  {t("translation:default.buttons.sendRFQ")}
                </Button>
              </Col>
            </Row>
            <Row gutter={5} className="d-flex mt-15">
              <Col sm={6}></Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rfiRespondDate")}
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {getLabelFromDateTime(
                      TransportationProvider?.rfiResponseDate
                    )}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6}>
                <FiledCard
                  title={t("translation:procurement.tenderCard.rfqRespondDate")}
                >
                  <PrimaryLabel normal={true} className="font-weight-400">
                    {getLabelFromDateTime(
                      TransportationProvider?.rfpResponseDate
                    )}
                  </PrimaryLabel>
                </FiledCard>
              </Col>
              <Col sm={6} className="d-flex justify-content-end">
                <Button onClick={() => sendRFIRFQHandler("Both")}>
                  {t("translation:default.buttons.sendRFIRFQ")}
                </Button>
              </Col>
            </Row>
          </Fragment>
        </Card>
      </Col>
    </Row>
  );
};

export default TransportationProviderCard;
