import React, { useMemo } from "react";
import { Col, Row, Spin } from "antd";
import ProcumentProjectListFilter from "./Component/Header/ProcurementProjectListFilter";
import { ReactComponent as CopyIcon } from "../../../../assets/icons/copy.svg";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import { Link } from "react-router-dom";
import { APP_ROUTES } from "../../../../contants/RoutesEnum";
import Button from "../../../components/Buttons/Button";
import { useDispatch, useSelector } from "react-redux";
import {
  getProjectCopyInProgress,
  getProjectCopyRequestReset,
} from "../../../../redux/Procurement/Project/ProcurementProjectActions";
import { useLocation, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const ProcurementProjectHeader = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { activeProject, projectCopy } = useSelector(
    (state) => state.procurementProject
  );
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const projectCopyHandler = () => {
    dispatch(getProjectCopyRequestReset());
    dispatch(
      getProjectCopyInProgress({
        projectID: activeProject?.data?.projectID || "",
      })
    );
    navigate(APP_ROUTES.ADD_PROJECT_PROCUREMENT);
  };
  const AddProject = () => {
    dispatch(getProjectCopyRequestReset());
    navigate(APP_ROUTES.ADD_PROJECT_PROCUREMENT);
  };

  return (
    <Row justify="space-between" className={`py-15 px-14 align="middle`}>
      <Col className="flex-1">
        {projectCopy.progressing === true && (
          <Spin
            className="d-flex justify-content-center align-items-center h-100"
            size={"large"}
            tip={t("translation:default.loading")}
            spinning={true}
          />
        )}
        {projectCopy.progressing === false && (
          <Row align={"middle"} gutter={20}>
            <Col sm={12}>
              <ProcumentProjectListFilter />
            </Col>
            <Col sm={12}>
              <div className="d-flex justify-content-end gap-2 align-items-center">
                <Button onClick={() => AddProject()}>
                  {t("translation:procurement.createProject")}
                </Button>

                <div
                  className="d-flex justify-content-end align-items-center cursor-pointer"
                  onClick={() => projectCopyHandler()}
                >
                  <CopyIcon />
                  <PrimaryLabel normal={true} className="font-weight-400 ml-5">
                    {t("translation:default.buttons.copy")}
                  </PrimaryLabel>
                </div>
              </div>
            </Col>
          </Row>
        )}
      </Col>
    </Row>
  );
};

export default ProcurementProjectHeader;
