import { Col, Row, Spin } from "antd";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getTenderByProjectListInProgress,
  getTenderByProjectListRequestReset,
} from "../../../../../../../redux/Procurement/Project/ProcurementProjectActions";
import {
  getDateLabelFormat,
  getLabelFromDateTime,
} from "../../../../../../../utilz/dates";
import Button from "../../../../../../components/Buttons/Button";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import EmptyRecords from "../../../../../../components/Common/EmptyRecords";
import PrimaryLabel from "../../../../../../components/Labels/PrimaryLabel";
import Paper from "../../../../../../components/Paper/Paper";
import { useLocation, useNavigate, createSearchParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { APP_ROUTES } from "../../../../../../../contants/RoutesEnum";

export default function ProcurementTenderDetailsTab(props) {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { tenderByProjectList } = useSelector(
    (state) => state.procurementProject
  );
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (props?.data?.projectID) {
      dispatch(getTenderByProjectListRequestReset());
      dispatch(
        getTenderByProjectListInProgress({
          projectID: props?.data?.projectID,
        })
      );
    }
  }, [props?.data]);

  const viewTender = (tenderId) => {
    navigate({
      pathname: APP_ROUTES.PROCUREMENT_TENDER,
      search: createSearchParams({
        tenderId: tenderId,
      }).toString(),
    });
    // navigate(APP_ROUTES.PROCUREMENT_TENDER);
  };
  console.log(
    "tenderByProjectList?.data?.tenderId",
    tenderByProjectList?.data
  );
  return (
    <Row className="h-100">
      <Col
        sm={24}
        className="h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
      >
        <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
          <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
            <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
              {tenderByProjectList?.data?.map((list, index) => (
                <Paper
                  key={index}
                  className={`shipment-card border-muted auction-card-active`}
                >
                  <Row gutter={15}>
                    <Col sm={24}>
                      <Row className="d-flex align-items-center p-15">
                        <Col sm={8}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.tenderName.label"
                            )}
                            textClass={
                              "d-flex align-items-center gap-1 white-space-nowarp"
                            }
                          >
                            <PrimaryLabel className="font-weight-600">
                              {list.tenderName}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={8}>
                          <FiledCard
                            title={t(
                              "translation:procurement.projectCard.currentRound"
                            )}
                            textClass={
                              "d-flex align-items-center gap-1 white-space-nowarp"
                            }
                          >
                            <PrimaryLabel className="font-weight-600">
                              {list.tenderCurrentRoundcount}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col
                          sm={8}
                          className="d-flex align-items-center justify-content-end"
                          style={{ gap: "5px" }}
                        >
                          <Button onClick={() => viewTender(list?.tenderID)}>
                            {t("translation:default.buttons.viewMoreDetails")}
                          </Button>
                        </Col>
                      </Row>
                      <hr className="m-0" />
                      <Row className="p-15">
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:procurement.fields.date.startDate"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {getLabelFromDateTime(list.tenderstartDate)}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:procurement.projectCard.modeOfTender"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.modeofTenderDesc}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.cargoType.label"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.cargoType}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.equipmentType.label"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.equipmentType}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                      <Row className="p-15">
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:procurement.fields.date.endDate"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {getLabelFromDateTime(list.tenderendDate)}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:procurement.fields.transportationMode.label"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.transportationMode}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:procurement.projectCard.serviceCategory"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.serviceCategory}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                        <Col sm={6}>
                          <FiledCard
                            title={t(
                              "translation:default.fields.currency.label"
                            )}
                          >
                            <PrimaryLabel
                              normal={true}
                              className="font-weight-400"
                            >
                              {list.currency}
                            </PrimaryLabel>
                          </FiledCard>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </Paper>
              ))}
              {tenderByProjectList?.data?.length === 0 &&
                tenderByProjectList?.progressing === false && <EmptyRecords />}
              {tenderByProjectList?.data?.length === 0 &&
                tenderByProjectList?.progressing === true && (
                  <Spin
                    className="d-flex justify-content-center align-items-center h-100"
                    size={"large"}
                    tip={t("translation:default.loading")}
                    spinning={true}
                  />
                )}
            </div>
          </div>
        </div>
      </Col>
    </Row>
  );
}
