import { Col, Row } from "antd";
import React, { useState, useMemo, useEffect } from "react";
import Button from "../../../../../components/Buttons/Button";
import SaveCancelButtons from "../../../../../components/Buttons/SaveCancelButtons";
import FiledCard from "../../../../../components/Cards/FiledCard";
import BatchLabel from "../../../../../components/Labels/BatchLabel";
import PrimaryLabel from "../../../../../components/Labels/PrimaryLabel";
import Paper from "../../../../../components/Paper/Paper";
import Tab from "../../../../../components/Tabs/Tab";
import ProcurementFileDetailsTab from "./Tabs/ProcurementFileDetailsTab";
import ProcurementProjectDetailsTab from "./Tabs/ProcurementProjectDetailsTab";
import ProcurementTenderDetailsTab from "./Tabs/ProcurementTenderDetailsTab";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../utilz/i18n";

function ProcurementProjectDetailCardContainer(props) {
  const [activeTabName, setActiveTabName] = useState("project");
  const [updateProject, setUpdateProject] = useState(null);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const [isUpdateProject, setIsUpdateProject] = useState(false);
  const [buttoncolor, setbutton] = useState(false);

  useEffect(() => {
    if (props.projectEdit === true || props.projectEdit === false) {
      setIsUpdateProject(false);
    }
  }, [props.projectEdit]);

  return (
    <Paper className=" pt-15 pb-15  border-muted h-100">
      <Row className=" ml-15">
        <Col sm={8}>
          <FiledCard title={t("translation:default.fields.projectName.label")}>
            <PrimaryLabel className="text-highlight font-weight-600">
              {props?.data?.projectName}
            </PrimaryLabel>
          </FiledCard>
        </Col>
        <Col sm={4}>
          <FiledCard
            title={t("translation:procurement.fields.projectStatus.label")}
          >
            <BatchLabel className={"mt-4 bg-success"}>
              {props?.data?.isActive === true
                ? t("translation:procurement.fields.projectStatus.true")
                : t("translation:procurement.fields.projectStatus.false")}
            </BatchLabel>
          </FiledCard>
        </Col>
      </Row>
      <hr />
      <Row className="h-100">
        <Col
          sm={24}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div
            className={`h-100 overflow-y-inherit d-flex flex-direction-column`}
          >
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-hidden mb-15 flex-1">
                <Tab
                  className={"visibility-tabs"}
                  onChange={(activeKey) => {
                    setActiveTabName(activeKey);
                  }}
                  activeKey={activeTabName}
                  defaultActiveKey="general"
                  contents={[
                    {
                      title: t("translation:procurement.projectCard.details"),
                      content: (
                        <ProcurementProjectDetailsTab
                          {...props}
                          isEdit={isUpdateProject}
                          updateProject={updateProject}
                          callbackEditProject={() => {
                            setIsUpdateProject(false);
                          }}
                        />
                      ),
                      contentKey: "project",
                    },
                    {
                      title: t(
                        "translation:procurement.projectTabs.tenderDetails"
                      ),
                      content: <ProcurementTenderDetailsTab {...props} />,
                      contentKey: "tender",
                    },
                    {
                      title: t("translation:default.documentManager.title"),
                      content: (
                        <ProcurementFileDetailsTab
                          {...props}
                          btnClikedStatus={buttoncolor}
                          addNewFileBtnEnable={(val) => {
                            setbutton(val);
                          }}
                        />
                      ),
                      contentKey: "file",
                    },
                  ]}
                ></Tab>
              </div>
            </div>
          </div>
          {activeTabName == "project" && isUpdateProject === false && (
            <div className="visibility-file-add-new-file-btn">
              <Button onClick={() => setIsUpdateProject(true)}>
                {t("translation:default.buttons.edit")}
              </Button>
            </div>
          )}
          {activeTabName == "project" && isUpdateProject === true && (
            <div className="visibility-file-add-new-file-btn">
              <SaveCancelButtons
                onCancel={() => {
                  setIsUpdateProject(false);
                }}
                onSave={() => setUpdateProject(!updateProject)}
              />
            </div>
          )}
          {activeTabName == "file" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                onClick={() => {
                  setbutton(true);
                }}
                className={`${
                  buttoncolor ? "" : "auction-btn"
                } w-100  border-radion-5`}
                disabled={buttoncolor}
                buttonType="button"
                type="primary"
              >
                {t("translation:default.documentManager.addNewFile")}
              </Button>
            </div>
          )}
        </Col>
      </Row>
    </Paper>
  );
}

export default ProcurementProjectDetailCardContainer;
