import React, { useMemo } from "react";
import { Col, Row } from "antd";
import { useSelector } from "react-redux";

import VerticalLine from "../../../components/Bars/VerticalLine";
import FooterContentCard from "../../common/FooterContentCard";
import { ReactComponent as FilesIcon } from "../../../../assets/icons/files-records.svg";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const ProcurementProjectFooter = () => {
  const { projectList } = useSelector((state) => state.procurementProject);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Row
      justify={"space-around"}
      className=" footer-container background-header px-20  py-12"
    >
      <Col>
        <FooterContentCard
          count={projectList?.totalActive || 0}
          label={t('translation:procurement.footer.totalActive')}
          Icon={FilesIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={projectList?.totalApproved || 0}
          label={t('translation:procurement.footer.totalApproved')}
          Icon={FilesIcon}
        />
      </Col>
      <Col>
        <VerticalLine />
      </Col>
      <Col>
        <FooterContentCard
          count={projectList?.totalCount || 0}
          label={t('translation:procurement.footer.total')}
          Icon={FilesIcon}
        />
      </Col>
    </Row>
  );
};

export default ProcurementProjectFooter;
