import { Fragment, useEffect, useState } from "react";
import { Col, Row } from "antd";
import ProcurementProjectDetailCardContainer from "./Component/DetailsCard/ProcurementProjectDetailCardContainer";
import ProcurementProjectMinCard from "./Component/MinCard/ProcurementProjectMinCard";
import ScrollPagination from "../../../components/Paginations/ScrollPagination";
import { useDispatch, useSelector } from "react-redux";
import {
  getProcurementProjectListInProgress,
  resetActiveProjectItemsRequest,
  setActiveProjectItemsRequest,
  setSelectedProjectAction,
  updateProcurementProjectInfoRequest,
} from "../../../../redux/Procurement/Project/ProcurementProjectActions";

const ProcurementProjectContent = () => {
  const { projectList, selectedProject } = useSelector((state) => state.procurementProject);
  const dispatch = useDispatch();
  const [projectEdit, setProjectEdit] = useState(false);

  useEffect(() => {
    if (projectList?.data?.length === 0) {
      dispatch(setSelectedProjectAction(null));
      dispatch(resetActiveProjectItemsRequest());
      dispatch(setActiveProjectItemsRequest(projectList.data[0]));
    }
    if (projectList?.data?.length > 0 && selectedProject === null) {
      dispatch(resetActiveProjectItemsRequest());
      dispatch(setActiveProjectItemsRequest(projectList.data[0]));
      dispatch(setSelectedProjectAction(projectList.data[0]));
    }
  }, [projectList]);

  const handleLoadMore = () => {
    dispatch(
      updateProcurementProjectInfoRequest({
        ...projectList.req,
        pageNo: projectList.req.pageNo + 1,
      })
    );

    dispatch(
      getProcurementProjectListInProgress({
        ...projectList.req,
        pageNo: projectList.req.pageNo + 1,
      })
    );
  };

  return (
    <Fragment>
      <Row gutter={15} className={"h-100 overflow-y-inherit mt-15"}>
        <Col
          sm={8}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <ScrollPagination
                className={`${
                  projectList?.data?.length > 0 ? "mb-15" : "pb-15"
                } h-100`}
                loadMore={handleLoadMore}
                hasMore={
                  projectList?.progressing
                    ? false
                    : projectList?.data?.length < projectList?.totalCount
                }
                isLoading={
                  projectList.progressing === true &&
                  projectList.data.length > 0
                }
                scrollOffset={100}
                isVirtualizationApproach={false}
              >
                {projectList?.data?.map((item, index) => (
                  <ProcurementProjectMinCard
                    key={index}
                    item={item}
                    data={selectedProject}
                    handlerSelectedProject={(item) => {
                      dispatch(setSelectedProjectAction(item));
                      dispatch(resetActiveProjectItemsRequest());
                      dispatch(setActiveProjectItemsRequest(item));
                      setProjectEdit(!projectEdit);
                    }}
                  />
                ))}
              </ScrollPagination>
            </div>
          </div>
        </Col>
        <Col
          sm={16}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
                {selectedProject != null && (
                  <ProcurementProjectDetailCardContainer
                    data={selectedProject}
                    projectEdit={projectEdit}
                    setProjectEdit={setProjectEdit}
                  />
                )}
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Fragment>
  );
};

export default ProcurementProjectContent;
