import React from "react";
import {
  Chart,
  SeriesTemplate,
  CommonSeriesSettings,
  Label,
  Size,
  ArgumentAxis,
  Tooltip,
} from "devextreme-react/chart";

import { Spin } from "antd";
import { getStyleVariableValue, uniqueID } from "../../../utilz/utilz";

const BarChart = ({ data = null, chartHeight, barWidth = null }) => {
  const customizeBottomLabel = (data) => {
    return data.valueText;
  };
  return (
    <Spin spinning={!data} id={uniqueID()}>
      {data && (
        <Chart dataSource={data} id={uniqueID()} className={"chart-container "}>
          <Tooltip enabled={true} id={uniqueID()} />
          <CommonSeriesSettings
            id={uniqueID()}
            argumentField="label"
            valueField={"value"}
            type="bar"
            ignoreEmptyPoints={true}
            showInLegend={false}
            barWidth={barWidth || 20}
            barPadding={0.5}
          />
          <SeriesTemplate id={uniqueID()} nameField="label"></SeriesTemplate>
          <ArgumentAxis id={uniqueID()} maxValueMargin={0.01}>
            <Label
              id={uniqueID()}
              visible={true}
              customizeText={customizeBottomLabel}
            />
          </ArgumentAxis>

          <Tooltip
            id={uniqueID()}
            enabled={true}
            contentRender={TooltipTemplate}
            color={getStyleVariableValue("--dark")}
            border={0}
            paddingLeftRight={0}
            paddingTopBottom={0}
            cornerRadius={3}
          />
          <Size id={uniqueID()} height={chartHeight} />
        </Chart>
      )}
    </Spin>
  );
};

export default BarChart;

function TooltipTemplate(info) {
  return (
    <div
      id={uniqueID()}
      className="state-tooltip px-16 py-10"
      style={{ zIndex: 999999999999 }}
    >
      <h4 id={uniqueID()} className="text-white">
        {info.argument} : {info.value}
      </h4>
    </div>
  );
}
