import { Col, Form, Row, Spin } from "antd";
import React, { Fragment, useEffect, useState, useMemo } from "react";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import BatchLabel from "../../../../../../components/Labels/BatchLabel";
import PrimaryLabel from "../../../../../../components/Labels/PrimaryLabel";
import { ReactComponent as StepIcon } from "./../../../../../../../assets/icons/step.svg";
import { ReactComponent as StartDateIcon } from "./../../../../../../../assets/icons/start-date.svg";
import { ReactComponent as PreparationFileIcon } from "./../../../../../../../assets/icons/preparation-file.svg";
import { ReactComponent as BidPhaseIcon } from "./../../../../../../../assets/icons/bid-phase.svg";
import { ReactComponent as SelectionPhaseIcon } from "./../../../../../../../assets/icons/selection-phase.svg";
import { ReactComponent as EndDateIcon } from "./../../../../../../../assets/icons/end-date.svg";
import FormSelectBox from "../../../../../../components/Inputs/FormSelectBox";
import FormTextArea from "../../../../../../components/Inputs/FormTextArea";
import FormLabel from "../../../../../../components/Labels/FormLabel";
import FormDatePicker from "../../../../../../components/Inputs/FormDatePicker";
import FormTimePicker from "../../../../../../components/Inputs/FormTimePicker";
import {
  dateTimeToSingleValue,
  getDateLabelFormat,
} from "../../../../../../../utilz/dates";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import {
  createUpdateProjectInProgress,
  createUpdateProjectInSuccess,
  getProcurementProjectListInProgress,
  getProcurementProjectListInSuccess,
  getProcurementProjectListRequestReset,
  getProcurementProjectListUpdate,
  setSelectedProjectAction,
} from "../../../../../../../redux/Procurement/Project/ProcurementProjectActions";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { triggerNotification } from "../../../../../../../utilz/utilz";
import { NOTIFICATION_TYPES } from "../../../../../../../contants/common";
import { getProjectListEffect } from "../../../../../../../redux/Procurement/Project/ProcurementProjectEffects";

export default function ProcurementProjectDetailsTab(props) {
  const [form] = Form.useForm();
  const [projectLeadOption, setProjectLeadOption] = useState([]);
  const [techLeadOption, setTechLeadOption] = useState([]);
  const [projectSupportOption, setProjectSupportOption] = useState([]);
  const startDate = Form.useWatch("startDate", form);
  const preparationFromDate = Form.useWatch("preparationFromDate", form);
  const preparationToDate = Form.useWatch("preparationToDate", form);
  const bidCollcetionFromDate = Form.useWatch("bidCollcetionFromDate", form);
  const bidcollectionToDate = Form.useWatch("bidcollectionToDate", form);
  const selectionFromDate = Form.useWatch("selectionFromDate", form);
  const selectionToDate = Form.useWatch("selectionToDate", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const dispatch = useDispatch();
  const notificationAPI = useSelector((state) => state.common.notificationAPI);

  const { userProcurement, createUpdateProject, projectList } = useSelector(
    (state) => state.procurementProject
  );
  if (!props.isEdit) {
    form.resetFields();
  }

  useEffect(() => {
    if (
      createUpdateProject?.success === true &&
      Object.keys(createUpdateProject?.data).length > 0
    ) {
      if (
        (createUpdateProject?.data?.result || "").toLowerCase() == "success"
      ) {
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: "Update Successfully.",
        });
        props.callbackEditProject();
        updateListRecord(createUpdateProject?.data?.projectid);
      }
    }
  }, [createUpdateProject]);

  const updateListRecord = async (projectID) => {
    let { data } = await getProjectListEffect({
      projectId: projectID,
      isApproved: true,
    });
    let findItem = projectList?.data?.filter(
      (item) =>
        item.projectID == data?.result?.[0]?.projectID &&
        item.isActive == data?.result?.[0]?.isActive
    );
    if (findItem.length > 0) {
      const index = projectList?.data?.findIndex(
        (obj) => obj.projectID == projectID
      );
      let temp = projectList?.data;
      if (index !== -1) {
        temp[index] = data?.result[0];
      }
      dispatch(getProcurementProjectListUpdate({ data: temp }));
      dispatch(setSelectedProjectAction(data?.result?.[0] || null));
    } else {
      dispatch(getProcurementProjectListRequestReset());
      dispatch(
        getProcurementProjectListInProgress({
          ...projectList.req,
          pageNo: 1,
        })
      );
    }
    dispatch(
      createUpdateProjectInSuccess({
        data: {},
      })
    );
  };

  const updateProjectDetails = () => {
    let formData = { ...form.getFieldValue() };
    let req = {
      projectID: props.data.projectID,
      projectName: props.data.projectName,
      action: t("translation:default.buttons.update"),
      description: formData.desc,
      projectTechnicalSupportList: formData?.techLead?.toString()
      ?.split(","),
      projectSupportList: formData?.projectSupport?.toString()
      ?.split(","),
      projectLeadList: formData?.projectLead?.toString()
      ?.split(","),
      isApproved: formData?.isApproved || props?.data?.isApproved,
      isActive: formData?.isActive || false,
      startDate: dateTimeToSingleValue(
        formData?.startDate || "",
        formData?.startTime || ""
      ),

      preparationFrom: dateTimeToSingleValue(
        formData?.preparationFromDate || "",
        formData?.preparationFromTime || ""
      ),
      preparationTo: dateTimeToSingleValue(
        formData?.preparationToDate || "",
        formData?.preparationToTime || ""
      ),
      bidCollcetionFrom: dateTimeToSingleValue(
        formData?.bidCollcetionFromDate || "",
        formData?.bidCollcetionFromTime || ""
      ),
      bidcollectionTo: dateTimeToSingleValue(
        formData?.bidcollectionToDate || "",
        formData?.bidcollectionToTime || ""
      ),
      selectionFrom: dateTimeToSingleValue(
        formData?.selectionFromDate || "",
        formData?.selectionFromTime || ""
      ),
      selectionTo: dateTimeToSingleValue(
        formData?.selectionToDate || "",
        formData?.selectionToTime || ""
      ),

      endDate: dateTimeToSingleValue(
        formData?.endDate || "",
        formData?.endTime || ""
      ),
    };
    dispatch(createUpdateProjectInProgress(req));
  };

  useEffect(() => {
    const mapData = (e) =>
      userProcurement?.data?.[e]?.map(({ userName, email, userId }) => ({
        label: `${userName} (${email})`,
        value: userId,
      }));

    setProjectSupportOption(mapData("22"));
    setTechLeadOption(mapData("23"));
    setProjectLeadOption(mapData("21"));

    form.setFieldsValue({ isActive: true });
    form.setFieldsValue({ isApproved: false });
  }, [userProcurement]);

  useEffect(() => {
    if (props?.updateProject != null) {
      form.submit()
    }
  }, [props?.updateProject]);

console.log('===> props', props.data)
  return (
    <Row className="h-100">
      <Col
        sm={24}
        className="h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
      >
        <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
          <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
            <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
              {createUpdateProject.progressing === true && (
                <div className="d-flex justify-content-center align-items-center h-100">
                  <Spin tip={t("translation:default.loading")}></Spin>
                </div>
              )}
              <Form
                onFinish={(data) => updateProjectDetails(data)}
                form={form}
                initialValues={{
                  isActive: props?.data?.isActive,
                  isApproved: props?.data?.isApproved,
                  desc: props?.data?.description,
                  projectSupport:
                  props?.data?.projectSupportList?.map((item) => item.userID) ?? [],
                  techLead: props?.data?.projectTechnicalSupportList?.map((item) => item.userID) ?? [],
                  projectLead: props?.data?.projectLeadList?.map((item) => item.userID) ?? [],
                  tenderendDate: props?.data?.tenderendDate
                    ? moment(props.data?.tenderendDate)
                    : moment(),
                  tenderendTime: props?.data?.tenderendDate
                    ? moment(props.data?.tenderendDate)
                    : moment(),
                  tenderstartDate: props?.data?.tenderstartDate
                    ? moment(props.data?.tenderstartDate)
                    : moment(),
                  tenderstartTime: props?.data?.tenderstartDate
                    ? moment(props.data?.tenderstartDate)
                    : moment(),
                  startDate: props?.data?.startDate
                    ? moment(props.data?.startDate)
                    : moment(),
                  startTime: props?.data?.startDate
                    ? moment(props.data?.startDate)
                    : moment(),
                  preparationFromDate: props?.data?.preparationFrom
                    ? moment(props.data?.preparationFrom)
                    : moment(),
                  preparationFromTime: props?.data?.preparationFrom
                    ? moment(props.data?.preparationFrom)
                    : moment(),
                  preparationToDate: props?.data?.preparationTo
                    ? moment(props.data?.preparationTo)
                    : moment(),
                  preparationToTime: props?.data?.preparationTo
                    ? moment(props.data?.preparationTo)
                    : moment(),
                  bidCollcetionFromDate: props?.data?.bidCollcetionFrom
                    ? moment(props.data?.bidCollcetionFrom)
                    : moment(),
                  bidCollcetionFromTime: props?.data?.bidCollcetionFrom
                    ? moment(props.data?.bidCollcetionFrom)
                    : moment(),
                  bidcollectionToDate: props?.data?.bidcollectionTo
                    ? moment(props.data?.bidcollectionTo)
                    : moment(),
                  bidcollectionToTime: props?.data?.bidcollectionTo
                    ? moment(props.data?.bidcollectionTo)
                    : moment(),
                  selectionFromDate: props?.data?.selectionFrom
                    ? moment(props.data?.selectionFrom)
                    : moment(),
                  selectionFromTime: props?.data?.selectionFrom
                    ? moment(props.data?.selectionFrom)
                    : moment(),
                  selectionToDate: props?.data?.selectionTo
                    ? moment(props.data?.selectionTo)
                    : moment(),
                  selectionToTime: props?.data?.selectionTo
                    ? moment(props.data?.selectionTo)
                    : moment(),
                  endDate: props?.data?.endDate
                    ? moment(props.data?.endDate)
                    : moment(),
                  endTime: props?.data?.endDate
                    ? moment(props.data?.endDate)
                    : moment(),
                }}
              >
                <Row className="mb-15">
                  <Col sm={24}>
                    <div className="d-flex align-items-center w-100 gap-1">
                      <div className="font-size-12 font-weight-400">
                        {t("translation:procurement.projectCard.information")}
                      </div>
                      <hr className="w-82" />
                    </div>
                  </Col>
                </Row>
                <Row className="mb-15">
                  <Col sm={6}>
                    <FiledCard
                      title={t("translation:default.fields.projectName.label")}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {props?.data?.projectName}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t(
                          "translation:procurement.fields.projectStatus.label"
                        )}
                      >
                        <BatchLabel className={"mt-4 bg-success"}>
                          {props?.data?.isActive === true
                            ? t(
                                "translation:procurement.fields.projectStatus.true"
                              )
                            : t(
                                "translation:procurement.fields.projectStatus.false"
                              )}
                        </BatchLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormSelectBox
                        className="pr-15"
                        loading={false}
                        options={[
                          {
                            label: t(
                              "translation:procurement.fields.projectStatus.true"
                            ),
                            value: true,
                          },
                          {
                            label: t(
                              "translation:procurement.fields.projectStatus.false"
                            ),
                            value: false,
                          },
                        ]}
                        placeholder={t("translation:default.buttons.select")}
                        label={t(
                          "translation:procurement.fields.projectStatus.status"
                        )}
                        rules={[]}
                        name={"isActive"}
                        editMode={true}
                        margin={"mb-15"}
                      />
                    )}
                  </Col>
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t(
                          "translation:procurement.fields.projectLead.label"
                        )}
                      >
                        <PrimaryLabel normal={true} className="font-weight-400">
                          {props?.data?.projectLeadList?.map(item=>item.userName).join(',')}
                        </PrimaryLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormSelectBox
                        className="pr-15"
                        loading={false}
                        options={projectLeadOption}
                        placeholder={t("translation:default.buttons.select")}
                        label={t(
                          "translation:procurement.fields.projectLead.label"
                        )}
                        rules={[]}
                        name={"projectLead"}
                        editMode={true}
                        margin={"mb-15"}
                        mode="multiple"
                      />
                    )}
                  </Col>
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t(
                          "translation:procurement.fields.tecSupport.label"
                        )}
                      >
                        <PrimaryLabel normal={true} className="font-weight-400">
                          {props?.data?.projectTechnicalSupportList?.map(item=>item.userName).join(',')}
                        </PrimaryLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormSelectBox
                        className="pr-15"
                        loading={false}
                        options={techLeadOption}
                        placeholder={t("translation:default.buttons.select")}
                        label={t(
                          "translation:procurement.fields.tecSupport.label"
                        )}
                        rules={[]}
                        name={"techLead"}
                        editMode={true}
                        margin={"mb-15"}
                        mode="multiple"
                      />
                    )}
                  </Col>
                </Row>
                <Row className="mb-15">
                  <Col sm={6}>
                    <FiledCard
                      title={t(
                        "translation:procurement.fields.actualName.label"
                      )}
                    >
                      <PrimaryLabel normal={true} className="font-weight-400">
                        {props?.data?.projectName}
                      </PrimaryLabel>
                    </FiledCard>
                  </Col>
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t(
                          "translation:procurement.fields.projectApproved.label"
                        )}
                      >
                        <BatchLabel className={"mt-4 bg-success"}>
                          {props?.data?.isApproved === true
                            ? t("translation:default.options.yes")
                            : t("translation:default.options.no")}
                        </BatchLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormSelectBox
                        className="pr-15"
                        loading={false}
                        options={[
                          {
                            label: t("translation:default.options.yes"),
                            value: true,
                          },
                          {
                            label: t("translation:default.options.no"),
                            value: false,
                          },
                        ]}
                        placeholder={t("translation:default.buttons.select")}
                        label={t(
                          "translation:procurement.fields.projectApproved.label"
                        )}
                        rules={[]}
                        name={"isApproved"}
                        editMode={true}
                        margin={"mb-15"}
                      />
                    )}
                  </Col>
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t(
                          "translation:procurement.fields.projectSupport.label"
                        )}
                      >
                        <PrimaryLabel normal={true} className="font-weight-400">
                          {props?.data?.projectSupportList?.map(i=>i.userName).join(',')}
                        </PrimaryLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormSelectBox
                        className="pr-15"
                        loading={false}
                        options={projectSupportOption}
                        placeholder={t("translation:default.buttons.select")}
                        label={t(
                          "translation:procurement.fields.projectSupport.label"
                        )}
                        rules={[]}
                        name={"projectSupport"}
                        editMode={true}
                        margin={"mb-15"}
                        mode="multiple"

                      />
                    )}
                  </Col>
                </Row>
                <Row className="mb-15">
                  <Col sm={6}>
                    {props?.isEdit === false && (
                      <FiledCard
                        title={t("translation:default.fields.description")}
                      >
                        <PrimaryLabel normal={true} className="font-weight-400">
                          {props?.data?.description}
                        </PrimaryLabel>
                      </FiledCard>
                    )}
                    {props?.isEdit === true && (
                      <FormTextArea
                        label={t("translation:default.fields.description")}
                        editMode={true}
                        margin={"mb-14"}
                        name={"desc"}
                        placeholder={t(
                          "translation:default.fields.description"
                        )}
                        enableAutoFocus={false}
                        rows={4}
                        defaultValue={""}
                      />
                    )}
                  </Col>
                </Row>
                <Row className="mb-15">
                  <Col sm={24}>
                    <div className="d-flex align-items-center w-100 gap-1">
                      <div className="font-size-12 font-weight-400">
                        {t("translation:procurement.projectCard.mileStones")}
                      </div>
                      <hr className="w-88" />
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col sm={24}>
                    <ul className="mile-stone-container">
                      <li>
                        <div className="details">
                          <div className="desc">
                            {t("translation:procurement.projectCard.startDate")}
                          </div>
                          <div className="divider"></div>
                          <div className="icons">
                            <StartDateIcon />
                          </div>
                          <div className="detail-desc">
                            {props?.isEdit === false && (
                              <FiledCard
                                title={t(
                                  "translation:procurement.fields.date.startDate"
                                )}
                              >
                                <PrimaryLabel
                                  normal={true}
                                  className="font-weight-400"
                                >
                                  {getDateLabelFormat(props?.data?.startDate)}
                                </PrimaryLabel>
                              </FiledCard>
                            )}
                            {props?.isEdit === true && (
                              <Row className="mb-15">
                                <Col sm={24} className="pr-15">
                                  <FormLabel className="form-input-label">
                                    {t(
                                      "translation:procurement.projectCard.startDate"
                                    )}
                                  </FormLabel>
                                  <div className="d-flex gap-1">
                                    <FormDatePicker
                                      placeholder={t(
                                        "translation:procurement.fields.date.placeholder"
                                      )}
                                      margin={"mb-0"}
                                      name={"startDate"}
                                      // disableFuture={true}
                                      disabledTargetDate={preparationFromDate}
                                      rules={[
                                        {
                                          required: true,
                                          message: "Please enter the Start Date",
                                        },
                                      ]}
                                    />
                                    <FormTimePicker
                                      colon={true}
                                      editMode={true}
                                      singleLineMode={false}
                                      name={"startTime"}
                                      margin={"m-0"}
                                      rules={[{
                                        required:true
                                      }]}
                                    />
                                  </div>
                                </Col>
                              </Row>
                            )}
                          </div>
                        </div>
                      </li>
                      <li>
                        <div className="details">
                          <div className="desc">
                            {t(
                              "translation:procurement.projectCard.preparationFiles"
                            )}
                          </div>
                          <div className="divider"></div>
                          <div className="icons">
                            <PreparationFileIcon />
                          </div>
                          <div className="detail-desc">
                            {props?.isEdit === false && (
                              <Fragment>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.startDate"
                                  )}
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.preparationFrom
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.endDate"
                                  )}
                                  textClass="mt-6"
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.preparationTo
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                              </Fragment>
                            )}
                            {props?.isEdit === true && (
                              <div className="edit-datetime-input">
                                <Row className="mb-10">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.projectCard.startDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"preparationFromDate"}
                                        // disableFuture={true}
                                        disabledTargetDate={startDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"preparationFromTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                                <Row className="mb-15">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.fields.date.endDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"preparationToDate"}
                                        //disableFuture={true}
                                        disabledTargetDate={preparationFromDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"preparationToTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                              </div>
                            )}
                          </div>
                        </div>
                      </li>
                      <li>
                        <div className="details">
                          <div className="desc">
                            {t(
                              "translation:procurement.projectCard.bidCollectionPhase"
                            )}
                          </div>
                          <div className="divider"></div>
                          <div className="icons">
                            <BidPhaseIcon />
                          </div>
                          <div className="detail-desc">
                            {props?.isEdit === false && (
                              <Fragment>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.startDate"
                                  )}
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.bidCollcetionFrom
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.endDate"
                                  )}
                                  textClass="mt-6"
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.bidcollectionTo
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                              </Fragment>
                            )}
                            {props?.isEdit === true && (
                              <div className="edit-datetime-input">
                                <Row className="mb-10">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.fields.date.startDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"bidCollcetionFromDate"}
                                        //disableFuture={true}
                                        disabledTargetDate={preparationToDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"bidCollcetionFromTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                                <Row className="mb-15">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.fields.date.endDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"bidcollectionToDate"}
                                        //disableFuture={true}
                                        disabledTargetDate={
                                          bidCollcetionFromDate
                                        }
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"bidcollectionToTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                              </div>
                            )}
                          </div>
                        </div>
                      </li>
                      <li>
                        <div className="details">
                          <div className="desc">
                            {t(
                              "translation:procurement.projectCard.selectionPhase"
                            )}
                          </div>
                          <div className="divider"></div>
                          <div className="icons">
                            <SelectionPhaseIcon />
                          </div>
                          <div className="detail-desc">
                            {props?.isEdit === false && (
                              <Fragment>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.startDate"
                                  )}
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.selectionFrom
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                                <FiledCard
                                  title={t(
                                    "translation:procurement.fields.date.endDate"
                                  )}
                                  textClass="mt-6"
                                >
                                  <PrimaryLabel
                                    normal={true}
                                    className="font-weight-400"
                                  >
                                    {getDateLabelFormat(
                                      props?.data?.selectionTo
                                    )}
                                  </PrimaryLabel>
                                </FiledCard>
                              </Fragment>
                            )}

                            {props?.isEdit === true && (
                              <div className="edit-datetime-input">
                                <Row className="mb-10">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.projectCard.startDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"selectionFromDate"}
                                        //disableFuture={true}
                                        disabledTargetDate={bidcollectionToDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"selectionFromTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                                <Row className="mb-15">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.fields.date.endDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:procurement.fields.date.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"selectionToDate"}
                                        //disableFuture={true}
                                        disabledTargetDate={selectionFromDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"selectionToTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                              </div>
                            )}
                          </div>
                        </div>
                      </li>
                      <li>
                        <div className="details">
                          <div className="desc">
                            {t("translation:procurement.fields.date.endDate")}
                          </div>
                          <div className="divider"></div>
                          <div className="icons">
                            <EndDateIcon />
                          </div>
                          <div className="detail-desc">
                            {props?.isEdit === false && (
                              <FiledCard
                                title={t(
                                  "translation:procurement.fields.date.endDate"
                                )}
                              >
                                <PrimaryLabel
                                  normal={true}
                                  className="font-weight-400"
                                >
                                  {getDateLabelFormat(props?.data?.endDate)}
                                </PrimaryLabel>
                              </FiledCard>
                            )}
                            {props?.isEdit === true && (
                              <div className="edit-datetime-input">
                                <Row className="mb-15">
                                  <Col sm={24} className="pr-15">
                                    <FormLabel className="form-input-label">
                                      {t(
                                        "translation:procurement.fields.date.endDate"
                                      )}
                                    </FormLabel>
                                    <div className="d-flex gap-1">
                                      <FormDatePicker
                                        placeholder={t(
                                          "translation:default.fields.createdOn.placeholder"
                                        )}
                                        margin={"mb-0"}
                                        name={"endDate"}
                                        // disableFuture={true}
                                        disabledTargetDate={selectionToDate}
                                      />
                                      <FormTimePicker
                                        colon={true}
                                        editMode={true}
                                        singleLineMode={false}
                                        name={"endTime"}
                                        margin={"m-0"}
                                      />
                                    </div>
                                  </Col>
                                </Row>
                              </div>
                            )}
                          </div>
                        </div>
                      </li>
                    </ul>
                  </Col>
                </Row>
              </Form>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  );
}
