import React, { Fragment, useMemo } from "react";
import { Card, Col, Popconfirm, Row } from "antd";
import { ReactComponent as CloseIcon } from "../../../../../../../assets/icons/close-icon.svg";
import { ReactComponent as LocationIcon } from "../../../../../../../assets/icons/location-pin-green.svg";
import PrimaryLabel from "../../../../../../components/Labels/PrimaryLabel";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import SvgConverter from "../../../../../../components/Common/SvgConverter";
import SecondaryLabel from "../../../../../../components/Labels/SecondaryLabel";
import FiledCard from "../../../../../../components/Cards/FiledCard";
import FormLabel from "../../../../../../components/Labels/FormLabel";
import HorizontalBar from "../../../../../../components/Bars/HorizontalBar";
import SaveCancelButtons from "../../../../../../components/Buttons/SaveCancelButtons";

const LaneInformationCard = ({ 
  LaneInformation, 
  isNewRecord = false,
  handleDelete = () => {}, 
  saveLaneHandler = () => {},
 }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const handleDeleteConfirm = () => handleDelete(LaneInformation);

  return (
    <Row>
      <Col sm={24} className="h-100 pb-15 overflow-y-inherit d-flex flex-direction-column">
        <Card
          title={
            <Row>
              <Col sm={15}>
                <Row gutter={15} className="mb-5 mt-n5 flex-nowrap align-items-center">
                  <Col>
                    <FormLabel>{t("translation:procurement.tenderCard.laneName")}</FormLabel>
                    <PrimaryLabel className="text-success text-center ml-5">
                      {LaneInformation?.originName}
                    </PrimaryLabel>
                  </Col>

                  <Col className="flex-1 ">
                    <HorizontalBar />
                  </Col>

                  <Col>
                    <PrimaryLabel className="text-danger text-center ml-5 ">
                      {LaneInformation?.destinationName}
                    </PrimaryLabel>
                  </Col>
                </Row>
              </Col>
            </Row>
          }
          className="visibility-file-card"
          extra={
            <Fragment>
              {!isNewRecord && (
                  <div className="d-flex gap-1">
                    <Popconfirm
                      placement="topRight"
                      title={"Are you sure to delete?"}
                      onConfirm={handleDeleteConfirm}
                      okText="Yes"
                      cancelText="Cancel"
                      overlayClassName=""
                    >
                      <CloseIcon className="cursor-pointer" />
                    </Popconfirm>
                  </div>
              )}
              {isNewRecord && (
                <SaveCancelButtons
                  onSave={()=>saveLaneHandler(LaneInformation)}
                  onCancel={()=>handleDelete(LaneInformation)}
                  editMode={false}
                />
              )}
            </Fragment>
          }
        >
          <Row>
            <Col sm={12}>
              <Row gutter={15} className="mb-5 mt-n5 flex-nowrap">
                <Col>
                  <SecondaryLabel className={"f-14 text-success"}>
                    <SvgConverter Icon={LocationIcon} color="success" />
                    <span className="ml-5">
                      {t("translation:default.fields.origin")}
                    </span>
                  </SecondaryLabel>
                </Col>
                <Col className="flex-1 shipment-card-distance-container mr-15">
                  <hr className=" border-top-success" />
                </Col>
              </Row>
            </Col>
            <Col sm={12}>
              <Row gutter={15} className="mb-5 mt-n5 flex-nowrap">
                <Col>
                  <SecondaryLabel className={"f-14 text-danger"}>
                    <SvgConverter Icon={LocationIcon} color="danger" />
                    <span className="ml-5">
                      {t("translation:default.fields.destination")}
                    </span>
                  </SecondaryLabel>
                </Col>
                <Col className="flex-1 shipment-card-distance-container ">
                  <hr className=" border-top-danger" />
                </Col>
              </Row>
            </Col>
          </Row>

          <Row gutter={15} className="flex-nowrap">
            <Col sm={12} className="flex-1">
              <FiledCard
                title={LaneInformation?.originName}
                labelClass={"text-success fw-1000"}
              >
                <SecondaryLabel className={"f-14"}>
                  {LaneInformation?.originDetails}
                </SecondaryLabel>
              </FiledCard>
            </Col>

            <Col sm={12} className="flex-1">
              <FiledCard
                title={LaneInformation?.destinationName}
                labelClass={"text-danger fw-1000"}
              >
                <SecondaryLabel className={"f-14"}>
                  {LaneInformation?.destinationDetails}
                </SecondaryLabel>
              </FiledCard>
            </Col>
          </Row>
        </Card>
      </Col>
    </Row>
  );
};

export default LaneInformationCard;
