import { Tabs } from "antd";
import React, { useMemo } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { APP_ROUTES } from "../../../contants/RoutesEnum";
import ProcurementBidAnalysisPage from "./ProcurementBidAnalysisPage";
import ProcurementProjectPage from "./ProcurementProjectPage";
import ProcurementRouteConfigurationPage from "./ProcurementRouteConfigurationPage";
import ProcurementTenderPage from "./ProcurementTenderPage";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../utilz/i18n";

function ProcurementPage() {
  const { pathname: activeTab } = useLocation();
  const navigate = useNavigate();

  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  return (
    <Tabs
      className="page-level-tab-container"
      onChange={(key) => navigate(key)}
      activeKey={activeTab}
    >
      <Tabs.TabPane
        key={APP_ROUTES.PROCUREMENT}
        tab={t("translation:procurement.subMenu.project")}
      >
        <ProcurementProjectPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PROCUREMENT_TENDER}
        tab={t("translation:procurement.subMenu.tender")}
      >
        <ProcurementTenderPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PROCUREMENT_BID_ANALYSIS}
        tab={t("translation:procurement.subMenu.bidAnalysis")}
      >
        <ProcurementBidAnalysisPage />
      </Tabs.TabPane>
      <Tabs.TabPane
        key={APP_ROUTES.PROCUREMENT_ROUTE_CONFIG}
        tab={t("translation:procurement.subMenu.routeConfiguration")}
      >
        <ProcurementRouteConfigurationPage />
      </Tabs.TabPane>
    </Tabs>
  );
}

export default ProcurementPage;
