import React, { Fragment, useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import LaneInformationCard from "./LaneInformationCard";
import EmptyRecords from "../../../../../../components/Common/EmptyRecords";
import ContentSpinner from "../../../../../../components/Spinners/ContentSpinner";
import { deleteProcurementTenderLaneEffects } from "../../../../../../../redux/Procurement/Tender/ProcurementTenderEffects";
import { setAppLoader } from "../../../../../../../redux/common/CommonActions";
import { NOTIFICATION_TYPES } from "../../../../../../../contants/common";
import { triggerNotification } from "../../../../../../../utilz/utilz";
import {
  addNewLaneBtn,
  getProcurementLaneInformationInProgress,
  getProcurementLaneInformationRequestReset,
  getUpdateTenderProgress,
} from "../../../../../../../redux/Procurement/Tender/ProcurementTenderAction";
import CommonModal from "../../../../../../components/Modals/CommonModal";
import TenderLaneInformationInfoModal from "../../AddTender/TenderLaneInformationInfoModal";

export default function LaneInformationTab({ tenderInfo = {} }) {
  let tenderId = tenderInfo?.tenderID;
  let projectID = tenderInfo?.projectID;

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const dispatch = useDispatch();
  const { LaneInformationList } = useSelector(
    (state) => state?.procurementTender
  );
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const [selectedLaneItems, setSelectedLaneItems] = useState([]);
  const { addNewLaneBtnClicked } = useSelector(
    (state) => state.procurementTender
  );

  useEffect(() => {
    dispatch(getProcurementLaneInformationRequestReset());
    dispatch(getProcurementLaneInformationInProgress({ tenderId }));
  }, [tenderId]);

  const handleDeletedSelectedItem = (data) => {
    const otherRemovedItem = selectedLaneItems.filter(
      (item) => item.laneId !== data.laneId
    );
    setSelectedLaneItems([...otherRemovedItem]);
  };

  const deleteLaneHandler = async (data) => {
    try {
      const req = {
        projectID,
        tenderID: tenderId,
        tenderLanes: [{ laneID: data?.laneId }],
      };
      dispatch(setAppLoader(true));
      const response = await deleteProcurementTenderLaneEffects(req);
      dispatch(setAppLoader(false));
      if (response?.data?.success) {
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.SUCCESS,
          title: "Tender Lane Deleted Successfully.",
        });
        dispatch(getProcurementLaneInformationRequestReset());
        dispatch(getProcurementLaneInformationInProgress({ tenderId }));
      } else {
        triggerNotification(notificationAPI, {
          type: NOTIFICATION_TYPES.ERROR,
          title: response?.data?.message || "Tender Deleted Error",
        });
      }
    } catch (error) {
      dispatch(setAppLoader(false));
    }
  };

  
  const saveLaneHandler = (laneInfo) => {
    const existingLaneList = LaneInformationList?.data?.map((item) => ({
      laneID: item.laneId,
    }));
    const laneList = selectedLaneItems
      .filter((item) => item.laneId === laneInfo.laneId)
      .map((item) => ({ laneID: item.laneId }));

    const payload = {
      ProjectID: projectID,
      TenderID: tenderId,
      LaneDetails: [
        ...laneList,
        ...existingLaneList
      ],
    };
    dispatch(getUpdateTenderProgress(payload));
  };

  return (
    <Fragment>
      {addNewLaneBtnClicked && (
        <CommonModal
          className="vw-70 vh-85"
          open={true}
          title={`${t("translation:default.buttons.search")}/${t(
            "translation:default.buttons.add"
          )} ${t("translation:default.fields.transportationProvider.label")}`}
          okText={t("translation:default.buttons.export")}
          cancelText={t("translation:default.buttons.close")}
          onCancel={() => dispatch(addNewLaneBtn(false))}
          onClose={() => dispatch(addNewLaneBtn(false))}
          customFooter={true}
        >
          <TenderLaneInformationInfoModal
            selectedItemHandler={(data) => {
              dispatch(addNewLaneBtn(false));
              setSelectedLaneItems([...data]);
            }}
          />
        </CommonModal>
      )}

      <div className="h-82 overflow-y-inherit d-flex flex-direction-column">
        <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
          <div className="custom-scroll-bar overflow-x-hidden  flex-1 pl-15 pr-15">
            {selectedLaneItems.map((row, index) => (
              <LaneInformationCard
                LaneInformation={row}
                handleDelete={handleDeletedSelectedItem}
                key={index}
                isNewRecord={true}
                saveLaneHandler={saveLaneHandler}
              />
            ))}
            <Fragment>
              {LaneInformationList?.progressing ? (
                <ContentSpinner size="large" />
              ) : (
                <Fragment>
                  {LaneInformationList?.data?.length > 0 ? (
                    <Fragment>
                      {LaneInformationList?.data.map((row, index) => (
                        <LaneInformationCard
                          LaneInformation={row}
                          handleDelete={deleteLaneHandler}
                          key={index}
                        />
                      ))}
                    </Fragment>
                  ) : (
                    <EmptyRecords
                      title="There is no Bit records found!"
                      descriptionOne=""
                      descriptionTwo=""
                    />
                  )}
                </Fragment>
              )}
            </Fragment>
          </div>
        </div>
      </div>
    </Fragment>
  );
}
