import React, { useState, useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Row, Form } from "antd";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import FiledCard from "../../../../../components/Cards/FiledCard";
import PrimaryLabel from "../../../../../components/Labels/PrimaryLabel";
import Paper from "../../../../../components/Paper/Paper";
import Tab from "../../../../../components/Tabs/Tab";
import BidParametersTab from "./Tabs/BidParametersTab";
import LaneInformationTab from "./Tabs/LaneInformationTab";
import TenderDocumentationTab from "./Tabs/TenderDocumentationTab";
import TenderInformationTab from "./Tabs/TenderInformationTab";
import HistoricalBidTab from "./Tabs/HistoricalBidTab";
import TransportationProviderTab from "./Tabs/TransportationProviderTab";
import Button from "../../../../../components/Buttons/Button";
import SaveCancelButtons from "../../../../../components/Buttons/SaveCancelButtons";
import { addNewBidParameterBtn, addNewLaneBtn } from "../../../../../../redux/Procurement/Tender/ProcurementTenderAction";
import { routeFileSelector } from "../../../../../../utilz/i18n";

function ProcurementTenderDetailCardContainer(props) {
  const [activeTabName, setActiveTabName] = useState("tenderInformation");
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const dispatch = useDispatch();
  const { addnewbidparameteractions } = useSelector(
    (state) => state.procurementTender
  );
  const [buttoncolor, setbutton] = useState(false);
  const [TenderEdit, setTenderEdit] = useState(false);
  const [updateTenderForm] = Form.useForm();

  const handleAddNewFileBtnEnable = (val) => setbutton(val);

  const handleSaveCancelButtons = {
    onCancel: () => setTenderEdit(false),
    onSave: () => updateTenderForm.submit(),
  };

  useEffect(() => {
    dispatch(addNewBidParameterBtn(false))
  }, [])
  

  return (
    <Paper className="pt-15 pb-15  border-muted h-100">
      <Row className="pl-15">
        <Col sm={24}>
          <FiledCard title={t("translation:default.fields.tenderName.label")}>
            <PrimaryLabel className="text-highlight font-weight-600">
              {props?.data?.tenderName}
            </PrimaryLabel>
          </FiledCard>
        </Col>
      </Row>
      <hr />
      <Row className="h-100">
        <Col
          sm={24}
          className="h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
        >
          <div
            className={`h-100 overflow-y-inherit d-flex flex-direction-column`}
          >
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-hidden mb-15 flex-1">
                <Tab
                  className={`visibility-tabs ${
                    activeTabName === "tenderInformation" && !TenderEdit
                      ? "reduce-tab-width"
                      : "full-width-tab"
                  } 
                  ${
                    activeTabName === "tenderInformation" && TenderEdit
                      ? "reduce-tab-width-75"
                      : ""
                  }
                  ${
                    activeTabName === "documentation"
                      ? "reduce-tab-width-80"
                      : ""
                  }
                  ${
                    activeTabName === "laneInformation"
                      ? "reduce-tab-width-75"
                      : ""
                  }
                  ${
                    activeTabName === "bidParameters"
                      ? "reduce-tab-width-65"
                      : ""
                  }`}
                  onChange={(activeKey) => setActiveTabName(activeKey)}
                  activeKey={activeTabName}
                  defaultActiveKey="tenderInformation"
                  contents={[
                    {
                      title: t(
                        "translation:procurement.tenderTabs.tenderInformation"
                      ),
                      content: (
                        <TenderInformationTab
                          {...props}
                          isEdit={TenderEdit}
                          form={updateTenderForm}
                        />
                      ),
                      contentKey: "tenderInformation",
                    },
                    {
                      title: t(
                        "translation:procurement.tenderTabs.documentation"
                      ),
                      content: (
                        <TenderDocumentationTab
                          btnClikedStatus={buttoncolor}
                          addNewFileBtnEnable={handleAddNewFileBtnEnable}
                        />
                      ),
                      contentKey: "documentation",
                    },
                    {
                      title: t(
                        "translation:procurement.tenderTabs.laneInformation"
                      ),
                      content: (
                        <LaneInformationTab
                          tenderInfo={props?.data || {}}
                        />
                      ),
                      contentKey: "laneInformation",
                    },
                    {
                      title: t(
                        "translation:procurement.tenderTabs.bidParameters"
                      ),
                      content: <BidParametersTab tenderInfo={props?.data} />,
                      contentKey: "bidParameters",
                    },
                    {
                      title: t(
                        "translation:default.fields.transportationProvider.label"
                      ),
                      content: (
                        <TransportationProviderTab
                          tenderId={props?.data?.tenderID}
                        />
                      ),
                      contentKey: "transporationProvider",
                    },
                    {
                      title: "Historical Bid",
                      content: (
                        <HistoricalBidTab tenderId={props?.data?.tenderID} />
                      ),
                      contentKey: "historicalbid",
                    },
                  ]}
                ></Tab>
              </div>
            </div>
          </div>
          {activeTabName === "tenderInformation" && !TenderEdit && (
            <div className="visibility-file-add-new-file-btn">
              <Button onClick={() => setTenderEdit(true)}>
                {t("translation:default.buttons.edit")}
              </Button>
            </div>
          )}
          {activeTabName === "tenderInformation" && TenderEdit && (
            <div className="visibility-file-add-new-file-btn">
              <SaveCancelButtons {...handleSaveCancelButtons} />
            </div>
          )}
          {activeTabName === "documentation" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                onClick={() => setbutton(true)}
                className={`${
                  buttoncolor ? "" : "auction-btn"
                } w-100  border-radion-5`}
                disabled={buttoncolor}
                buttonType="button"
                type="primary"
              >
                {t("translation:default.documentManager.addNewFile")}
              </Button>
            </div>
          )}

          {activeTabName === "laneInformation" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                buttonType="button"
                type="primary"
                onClick={() => dispatch(addNewLaneBtn(true))}
              >
                {t("translation:default.documentManager.addNewLane")}
              </Button>
            </div>
          )}

          {activeTabName === "bidParameters" && (
            <div className="visibility-file-add-new-file-btn">
              <Button
                buttonType="button"
                type="primary"
                onClick={() => dispatch(addNewBidParameterBtn(true))}
                className={`${
                  addnewbidparameteractions.btnCliked ? "" : "auction-btn"
                } w-100  border-radion-5`}
                disabled={addnewbidparameteractions.btnCliked}
              >
                {t("translation:default.documentManager.addNewParameter")}
              </Button>
            </div>
          )}
        </Col>
      </Row>
    </Paper>
  );
}

export default ProcurementTenderDetailCardContainer;