import { Col, Row } from "antd";
import React, { useState, useMemo } from "react";
import Paper from "../../../../../components/Paper/Paper";
import Tab from "../../../../../components/Tabs/Tab";
import CombinationAnalysis from "./Tabs/CombinationAnalysis";
import DetailsTab from "./Tabs/DetailsTab";
import LaneCoverageAnalysisTab from "./Tabs/LaneCoverageAnalysisTab";
import LaneCoverageTab from "./Tabs/LaneCoverageTab";
import RanksAndCostTab from "./Tabs/RanksAndCostTab";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../utilz/i18n';

export default function ProcurementBidAnalysisDetailCardContainer() {
  const [activeTabName, setActiveTabName] = useState("rankAndCost");
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Paper className="pt-15 border-muted h-99">
      <Row className="h-100">
        <Col
          sm={24}
          className={"h-100 overflow-y-inherit d-flex flex-direction-column"}
        >
          <div
            className={`h-100 overflow-y-inherit d-flex flex-direction-column`}
          >
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-hidden  flex-1">
                <Tab 
                 className={"visibility-tabs"}
                  onChange={(activeKey) => {
                    setActiveTabName(activeKey);
                  }}
                  activeKey={activeTabName}
                  defaultActiveKey="general"
                  contents={[
                    {
                      title: t('translation:procurement.bigAnalysisCard.rankAndCost'),
                      content: <RanksAndCostTab />,
                      contentKey: "rankAndCost",
                    },
                    {
                      title: t('translation:procurement.bigAnalysisCard.laneCoverage'),
                      content: <LaneCoverageTab />,
                      contentKey: "laneCoverage",
                    },
                    {
                      title: t('translation:procurement.bigAnalysisCard.details'),
                      content: <DetailsTab />,
                      contentKey: "details",
                    },
                    {
                      title: t('translation:procurement.bigAnalysisCard.laneCoverageAnalysis'),
                      content: <LaneCoverageAnalysisTab />,
                      contentKey: "laneCoverageAnalysis",
                    },
                    {
                      title: t('translation:procurement.bigAnalysisCard.combinationAnalysis'),
                      content: <CombinationAnalysis />,
                      contentKey: "combinationAnalysis",
                    },
                  ]}
                ></Tab>
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Paper>
  );
}
