import { Col, Row } from "antd";
import { Fragment, useEffect, useState } from "react";
import ScrollPagination from "../../../components/Paginations/ScrollPagination";
import ProcurementTenderDetailCardContainer from "./Component/DetailsCard/ProcurementTenderDetailCardContainer";
import ProcurementTenderMinCard from "./Component/MinCard/ProcurementTenderMinCard";
import { useSelector, useDispatch } from "react-redux";
import {
  getProcurementTenderListInProgress,
  resetActiveTenderItemsRequest,
  setActiveTenderItemsRequest,
  updateProcurementTenderInfoRequest,
} from "../../../../redux/Procurement/Tender/ProcurementTenderAction";
const ProcurementTenderContent = () => {
  const { tenderList } = useSelector((state) => state.procurementTender);
  const [selectedTender, setSelectedTender] = useState();
  const dispatch = useDispatch();
  const handleLoadMore = () => {
    dispatch(
      updateProcurementTenderInfoRequest({
        ...tenderList.req,
        pageNo: tenderList.req.pageNo + 1,
      })
    );

    dispatch(
      getProcurementTenderListInProgress({
        ...tenderList.req,
        pageNo: tenderList.req.pageNo + 1,
      })
    );
  };

  useEffect(() => {
    if (tenderList?.data?.length === 0) {
      setSelectedTender(null);
      dispatch(resetActiveTenderItemsRequest());
    }
    if (tenderList?.data?.length > 0 && !selectedTender) {
      dispatch(resetActiveTenderItemsRequest());
      dispatch(setActiveTenderItemsRequest(tenderList.data[0]));
      setSelectedTender(tenderList.data[0]);
    }
  }, [tenderList]);
  return (
    <Fragment>
      <Row gutter={15} className={"h-100 overflow-y-inherit mt-15"}>
        <Col
          sm={8}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <ScrollPagination
                className={`${
                  tenderList?.data?.length > 0 ? "mb-15" : "pb-15"
                } h-100`}
                loadMore={handleLoadMore}
                hasMore={
                  tenderList?.progressing
                    ? false
                    : tenderList?.data?.length < tenderList?.totalCount
                }
                isLoading={
                  tenderList.progressing === true && tenderList.data.length > 0
                }
                scrollOffset={100}
              >
                {tenderList?.data?.map((item, index) => (
                  <ProcurementTenderMinCard
                    key={index}
                    item={item}
                    selectedTender={selectedTender}
                    handlerSelectedTender={(item) => {
                      setSelectedTender(item);
                      dispatch(resetActiveTenderItemsRequest());
                      dispatch(setActiveTenderItemsRequest(item));
                    }}
                  />
                ))}
              </ScrollPagination>
            </div>
          </div>
        </Col>
        <Col
          sm={16}
          className={
            "h-100 pb-15 overflow-y-inherit d-flex flex-direction-column"
          }
        >
          <div className="h-100 overflow-y-inherit d-flex flex-direction-column">
            <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
              <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
                {selectedTender && (
                  <ProcurementTenderDetailCardContainer data={selectedTender} />
                )}
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </Fragment>
  );
};

export default ProcurementTenderContent;
