import React, { useState, useMemo  } from "react";
import { Collapse } from "antd";
import GeneralDetails from "./Tender/GeneralDetails";
import Rank from "./Tender/Rank";
import SummarizeCritria from "./Tender/SummarizeCritria";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../../../utilz/i18n';

const { Panel } = Collapse;

export default function TenderSearch({ form }) {
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const [activeKey, setActiveKey] = useState("1");

  const handleTabClick = (keys) => {
    if (keys && keys[1]) {
      setActiveKey(keys[1]);
    } else {
      setActiveKey("");
    }
  };
  return (
    <Collapse
      ghost
      className="filter-collapse-container px-16"
      // defaultActiveKey={["1"]}
      activeKey={activeKey}
      onChange={handleTabClick}
    >
      <Panel header={`${t('translation:visibility.detailCard.generalTab.title')} ${t('translation:visibility.detailCard.tabs.details')}`} key="1">
        <GeneralDetails form={form} />
      </Panel>
      <Panel header={t('translation:procurement.filters.rank')} key="2">
        <Rank form={form} />
      </Panel>
      <Panel header={t('translation:procurement.filters.summarizeCritria')} key="3">
        <SummarizeCritria form={form} />
      </Panel>
    </Collapse>
  );
}
