import { Progress } from "antd";
import React from "react";
import { getStyleVariableValue, valueToNumber } from "../../../utilz/utilz";

const UtilizationBar = ({ value = 0, showInfo = true }) => {
  return (
    <Progress
      strokeLinecap="butt"
      percent={valueToNumber(value)}
      showInfo={showInfo}
      strokeColor={getStyleVariableValue("--distanceColor")}
      status={"active"}
      className="utilization-progress"
    />
  );
};

export default UtilizationBar;
