import React, { useEffect, useState,  useMemo} from "react";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';
import i18n from 'i18next';

const ProjectStatusSelector = ({
  placeholder = i18n.t('translation:procurement.fields.projectStatus.placeholder'),
  label = i18n.t('translation:procurement.fields.projectStatus.label'),
  rules = [],
  name,
  editMode,
  margin,
  id = "",
  tooltip = false,
  mode = "",
  loading = false,
  onChange = () => {}
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  
  return (
    <FormSelectBox
      loading={loading}
      options={[
        {
          label: t('translation:procurement.fields.projectStatus.true'),
          value: true,
        },
        {
          label: t('translation:procurement.fields.projectStatus.false'),
          value: false,
        },
      ]}
      placeholder={placeholder}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      id={id}
      tooltip={tooltip}
      mode={mode}
      onChange={onChange}
    />
  );
};

export default ProjectStatusSelector;
