import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

function ProcurementBidAnalysisPage() {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  
  return (
    <div className="w-100 h-100 overflow-hidden">
      <WithFilter
        isPageTitle={false}
        // headerContent={
        //   <ProcurementTenderHeader/>
        // }
        filterTitle={t('translation:procurement.filters.searchCriteria')}
        filterElement={<ProcurementBidAnalysisFilter />}
        contentElement={<ProcurementBidAnalysisContent />}
        footerContent={<ProcurementBidAnalysisFooter />}
      />
    </div>
  );
}
export default ProcurementBidAnalysisPage;
