import React, { useMemo } from "react";
import { Col, Form, Row } from "antd";
import { useDispatch } from "react-redux";
import { DEFAULT_DATE_FORMAT } from "../../../../contants/common";
import { PROCUREMENT_PROJECT_DATE_FIELDS } from "../../../../contants/Fields";
import {
  getProcurementProjectListInProgress,
  getProcurementProjectListRequestReset,
} from "../../../../redux/Procurement/Project/ProcurementProjectActions";
import getProjectListBySearchReq from "../../../../redux/Procurement/Project/requestPayload/GetProjectListBySearch";
import Button from "../../../components/Buttons/Button";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";
import ProcurementProjectFilterContent from "./Component/Search/ProcurementProjectFilterContent";

const ProcurementProjectFilter = () => {
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  const getProjectList = (data) => {
    dispatch(getProcurementProjectListRequestReset());

    for (const key in data) {
      if (PROCUREMENT_PROJECT_DATE_FIELDS.includes(key) && data[key]) {
        data[key] = data[key].format(DEFAULT_DATE_FORMAT);
      }
    }

    data.projectId = data?.projectId?.join(",");
    data.approvedBy = data?.approvedBy?.join(",");

    dispatch(
      getProcurementProjectListInProgress({
        ...data,
        pageSize: 10,
        pageNo: 1,
      })
    );
  };

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1">
        <Form
          onFinish={getProjectList}
          form={form}
          initialValues={{
            isActive: true,
            isApproved: true,
          }}
        >
          <ProcurementProjectFilterContent form={form} />
        </Form>
      </div>
      <Row className="px-15" gutter={15}>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"default"}
            onClick={() => form.resetFields()}
            buttonType="button"
          >
            {t("translation:default.buttons.clear")}
          </Button>
        </Col>
        <Col sm={24} md={12}>
          <Button
            fullWidth={true}
            type={"primary"}
            onClick={() => form.submit()}
            buttonType="submit"
          >
            {t("translation:default.buttons.search")}
          </Button>
        </Col>
      </Row>
    </div>
  );
};

export default ProcurementProjectFilter;
